import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Grid, GridItem } from 'vux'

export default {
    data() {
        return {
            mid: this.$route.query.mid,
            index: 0,
            imgsrc: "",
            username: "",
            useravatar: "",
            Key: this.getCookie("key"),//用户唯一标识

        }
    },
    components: {
        XHeader,
        Tab,
        XButton,
        Grid,
        GridItem,
        TabItem,
        Swiper,
        SwiperItem
    },

    created() {
        if (this.Key == '') {
            this.$router.replace('/login');
        }
        this.Getinfo();

    },
    mounted() {

    },
    methods: {


        Getinfo() {
            this.$axios.get('/index-inviteinfo.html?', {
                params: {
                    mid: this.mid,
                }
            }).then((res) => {
                console.log(res)
                if (res.data.code == 200) {
                    this.imgsrc = res.data.datas.invidesrc;
                    this.username = res.data.datas.user_name;
                    this.useravatar = res.data.datas.member_avatar;

                }
            })
                .catch((err) => {
                    console.log(err)
                })

        },
    }
}


