
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Scroll
},
name: 'cusevaluation',

data() {
return {
Eval_list: [],//评价列表
Goods_evaluate_info: [],//商品评价
CurrPage: 1, //当前页码
PageSize: 20, //每页数据量
totalCount: 0, //数据总条数
PageCount: 0, //总页数
LoadObj: false,//是否已经加载数据
hasMoreDataObj: false,//是否有下一页数据
pullDownRefresh: false,//是否启用下拉功能
pullDownRefreshObj: {
beforeTxt: "下拉刷新",
releaseRefreshTxt: "释放立即刷新",
txt: "加载中...",
threshold: 40,
stop: 0
},
pullUpLoad: true,//是否启用上拉功能
pullUpLoadObj: {
threshold: -40,
txt: { more: "查看更多", noMore: "我是有底线的" }
},
SearchLoad: false,
Goodsid: this.$route.query.goodsid,//商品ID
Type: '',//评论类型(0:全部 1:好评 2:中评 3:差评 4:订单晒图 5:追加评论)
Contenttype: true,//查看类型(true:查看有内容的评论false:查看所有)
}
},

created: function () {
this.GetGoodsDetailList();
this.GetEvalList();
},

mounted: function () {
},
filters: {
ChangeTime(value) {
if (value.length == 10) {
value = value * 1000;
}

var date = new Date(value);

var Y = date.getFullYear() + '-';
var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
var D = date.getDate() + ' ';
var h = date.getHours() + ':';
var m = date.getMinutes() + ':';
var s = date.getSeconds();
var newdate = Y + M + D + h + m + s;
return newdate;
},
},

methods: {

InitParams: function () {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Eval_list = [];
this.hasMoreDataObj = false;
},

onPullingDown: function () {
this.InitParams();
this.GetEvalList();
},

onPullingUp: function () {

if (this.hasMoreDataObj) {
this.GetEvalList();
}
},

ChangeType: function (type) {
this.Type = type;
this.InitParams();
this.GetEvalList();
},

GetEvalList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_evaluate.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
goods_id: this.Goodsid,
type: this.Type,
}
}).then((res) => {
if (res.data.code == 200) {
this.Eval_list.push(...res.data.datas.goods_eval_list);
console.log(res.data.datas);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = false;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetGoodsDetailList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_detail.html', {
params: {
goods_id: this.Goodsid,
key: '',
ivcode: 0,
}
}).then((res) => {
if (res.data.code == 200) {
this.Goods_evaluate_info = res.data.datas.goods_evaluate_info;
}
})
.catch((err) => {
console.log(err);
})
},
},
}


