
import BScroll from 'better-scroll';
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


export default {
components: {
Loading,
Alert,
},
name: 'home',

data() {
return {
Fiy_list: [], //列表
SearchLoad: false,
fullHeight: document.documentElement.clientHeight - 85, //自动计算页面高度
listHeight: [],
scrollY: 0,
}
},
computed: {
currentIndex() {
for (let i = 0; i < this.listHeight.length; i++) {
let height1 = this.listHeight[i];
let height2 = this.listHeight[i + 1];
if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
this._followScroll(i);
return i;
}
}
return 0;
}

},

created: function () {
this.GetFiyList();
},

mounted: function () {

},

methods: {
selectMenu(index, event) {
console.log("this.$refs.foodList+" + this.$refs.foodList)
if (!event._constructed) {
return;
}
let foodList = this.$refs.foodList;
let el = foodList[index];
this.foodsScroll.scrollToElement(el, 300);
},
selectFood(food, event) {
if (!event._constructed) {
return;
}
this.selectedFood = food;
this.$refs.food.show();
},
addFood(target) {
this._drop(target);
},
_drop(target) {

this.$nextTick(() => {
this.$refs.shopcart.drop(target);
});
},
_initScroll() {
this.meunScroll = new BScroll(this.$refs.menuWrapper, {
click: true
});

this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
click: true,
probeType: 3
});

this.foodsScroll.on('scroll', (pos) => {

if (pos.y <= 0) {
this.scrollY = Math.abs(Math.round(pos.y));
}
});
},
_calculateHeight() {
let foodList = this.$refs.foodList;
let height = 0;
this.listHeight.push(height);
for (let i = 0; i < foodList.length; i++) {
let item = foodList[i];
height += item.clientHeight;
this.listHeight.push(height);
}
},
_followScroll(index) {
let menuList = this.$refs.menuList;
let el = menuList[index];
this.meunScroll.scrollToElement(el, 300, 0, -100);
},

GetFiyList: function () {
this.SearchLoad = true;
this.$axios.get('/goods_class-index.html', {
params: {}
}).then((res) => {
if (res.data.code == 200) {

for (var i = 0; i < res.data.datas.class_list.length; i++) {
if (res.data.datas.class_list[i].gc_forbidden == 1) {
this.Fiy_list.push(res.data.datas.class_list[i]);
}
}
console.log(this.Fiy_list);
this.$nextTick(() => {
this._initScroll();
this._calculateHeight();
});
this.SearchLoad = false;
}
})
.catch((err) => {
console.log(err);
this.SearchLoad = false;
})
},

GetAllGoods: function (gc_id, gc_name) {
this.$router.push({
name: 'goodlist',
query: {
gc_id: gc_id,
title: gc_name,
}
});
},

GetNameGoods: function (gc_name) {
this.$router.push({
name: 'goodlist',
query: {
title: gc_name,
}
});
}
},
}


