
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
components: {
Loading,
Tab,
TabItem,
Swiper,
SwiperItem,
Popup,
Group,
XAddress,
XButton,
Cell
},
name: 'goodlistdetail',

data() {
return {
Goodsdetail_list: [],//商品详情
Goodsimg_list: [],//商品图片
Goods_hair_info: [],//快递详情
Goods_hair_infoobj: [],//选择地址后的快递信息
Goods_Pushinfo: [],//推荐商品
Voucher_list: [],//优惠券列表
Store_info: [],//店铺信息
Store_id: 0,//店铺id
Spec_list: [],//规格信息
Goods_evaluate_info: [],//总评价信息
Goods_eval_list: [],//评价列表
Goods_commend_list: [],//商品推荐列表
Store_credit: [],//商家评价信息
Is_favorate: false,//是否关注
GoodsCentent: '',//商品详情
Goodsid: this.$route.query.goodsid,//商品ID
Pushid: 0,//商品推荐id
SpecImg: '',//商品规格图片
Key: this.getCookie("key"),//用户唯一标识
SearchLoad: false,
IsSpec: false,//是否显示规格
IsVoch: false,//是否显示购物券
tabindex: 0,//默认从0开始
Buynow: 1,//是否显示立即购买按钮 1:显示 0:不显示(加价购不显示)
Buynow_text: '立即购买',//购买方式显示 立即购买 预售购买 支付定金 F码购买
Cart: 1,//是否显示加入购物车按钮 1:显示 0:不显示(虚拟、F码、预售、易货商品不显示加入购物车，不显示门店)
PayNum: 1,//购买数量
TotalNum: 10,//库存
Cart_count: 0,//购物车数量
Acspec: [],//选中的规格
AcspecText: '',//显示选中的规格

title: '',
value: [],
addressData: ChinaAddressV4Data,
showAddress: false,
Addressids: [],//获取地址id
}
},

created: function () {
this.GetGoodsDetailList();
this.GetCartCount();
},

mounted: function () {

},
filters: {
ChangeTime(value) {

if (value != undefined) {













value = value.replace('年', '.');
value = value.replace('月', '.');
value = value.replace('日', '');

var newdate = value;
return newdate;
}
},

numFilter(value) {
value = value / 2;
var fixNum = new Number(value + 1).toFixed(2);
var fixedNum = new Number(fixNum - 1).toFixed(0);
return fixedNum;
},

CheckEx(value) {
console.log(typeof (value))
var newvalue = '';
if (typeof (value) == 'string') {
newvalue = value;
} else if (typeof (value) == 'object') {

newvalue = value.postage;
}
return newvalue;
},

ExShow(value) {
console.log(value.ems);
console.log(value.express);
console.log(value.plainmail);
var newvalue = '';
if (value.express != undefined) {
newvalue = value.express;
} else if (value.ems != undefined) {
newvalue = value.ems;
} else if (value.plainmail != undefined) {
newvalue = value.plainmail;
}
return newvalue;
},
},

methods: {

GetGoodsDetailList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_detail.html', {
params: {
goods_id: this.Goodsid,
key: this.Key,
ivcode: 0,
}
}).then((res) => {
if (res.data.code == 200) {
this.PayNum = 1;
this.Goodsdetail_list = res.data.datas.goods_info;
console.log(res.data.datas.voucher);
if (res.data.datas.voucher != undefined) {
this.Voucher_list = res.data.datas.voucher;
}
console.log(this.Voucher_list);
this.Goods_Pushinfo = res.data.datas.push_info;
if (this.Goods_Pushinfo != '') {
this.Pushid = this.Goods_Pushinfo[0].push_id
}
this.Goodsimg_list = [];
res.data.datas.goods_image.split(",").forEach(item => {
this.Goodsimg_list.push({ 'img': item });
this.SpecImg = item;
})
this.TotalNum = this.Goodsdetail_list.goods_storage;
this.Goods_hair_info = res.data.datas.goods_hair_info;
this.Store_id = res.data.datas.store_info.store_id;
this.Store_credit = res.data.datas.store_info.store_credit;
this.Spec_list = res.data.datas.spec_list;
this.Goods_commend_list = res.data.datas.goods_commend_list;
this.Goods_evaluate_info = res.data.datas.goods_evaluate_info;
this.Goods_eval_list = res.data.datas.goods_eval_list;
this.Is_favorate = res.data.datas.is_favorate;
this.GoodsCentent = this.Goodsdetail_list.mobile_body;
this.Buynow = this.Goodsdetail_list.buynow;
this.Buynow_text = this.Goodsdetail_list.buynow_text == undefined ? '立即购买' : this.Goodsdetail_list.buynow_text;
this.Cart = this.Goodsdetail_list.cart;



this.AcspecText = '';
for (var spec in this.Goodsdetail_list.goods_spec) {
this.AcspecText = this.AcspecText + this.Goodsdetail_list.goods_spec[spec] + '、'
}
this.AcspecText = this.AcspecText.substring(0, this.AcspecText.length - 1);

this.GetVoucherList();
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetGoodsSpecId: function (index, value) {

this.Acspec = [];





for (var i in this.Goodsdetail_list.spec_value) {

for (var x in this.Goodsdetail_list.spec_value[i]) {
for (var y in this.Goodsdetail_list.goods_spec) {
if (x == y) {

this.Acspec.push({ 'specid': x });
}
}
}
}


console.log(this.Acspec);
this.Acspec.splice(index, 1, { 'specid': value });
this.Acspec.sort(this.sortBySpecid)
var AcspecId = '';
this.Acspec.forEach(item => {
AcspecId += item.specid + '|';
});
AcspecId = AcspecId.substring(0, AcspecId.length - 1);

for (var i in this.Spec_list) {
if (i == AcspecId) {
if (this.Spec_list[i] != this.Goodsid) {
console.log(this.Spec_list[i]);

this.$router.replace('/gooddetail?goodsid=' + this.Spec_list[i]);
this.Goodsid = this.Spec_list[i];
this.GetGoodsDetailList();
this.GetCartCount();
}
}
}
},

sortBySpecid: function (x, y) {
return x.specid - y.specid;
},

GetSpecType: function (index) {
for (var i in this.Goodsdetail_list.goods_spec) {
if (i == index) {
return true;
}
}
},

GetVoucherList: function () {
if (this.Key == '') {
return;
}
this.$axios.post('/voucher-voucher_tpl_list.html', this.$qs.stringify({
store_id: this.Store_id,
gettype: 'free',
key: this.Key,
goods_id: this.Goodsid,
})
).then((res) => {
if (res.data.code == 200) {
this.Store_info = res.data.datas.store_info;

console.log(this.Store_info.store_collect)
console.log('优惠券');
console.log(this.Voucher_list);
console.log('店铺信息');
console.log(this.Store_info);
}
})
.catch((err) => {
console.log(err);
})
},

GetVoucher: function (id) {
if (this.Key == '') {
return;
}
this.$axios.post('/member_voucher-voucher_freeex.html', this.$qs.stringify({
tid: id,
key: this.Key,
})
).then((res) => {
if (res.data.code == 200) {
if (res.data.login == 0) {
this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
} else {
this.$vux.toast.show({ text: '领取成功', type: 'text' });
}
} else {
this.$vux.toast.show({ text: res.data.datas.error, type: 'text', width: '20em' });
}
})
.catch((err) => {
console.log(err);
})
},

Addfavorites: function () {
if (this.Key == '') {
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.SearchLoad = true;
this.$axios.post('/member_favorites-favorites_add.html', this.$qs.stringify({
goods_id: this.Goodsid,
key: this.Key,
})
).then((res) => {
if (res.data.code == 200) {
this.SearchLoad = false;
this.Is_favorate = true;
this.$vux.toast.show({ text: '关注成功', type: 'text' });
}
})
.catch((err) => {
this.SearchLoad = false;
this.$vux.toast.show({ text: '关注失败', type: 'text' });
console.log(err);
})
},

Delfavorites: function () {
if (this.Key == '') {
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.SearchLoad = true;
this.$axios.post('/member_favorites-favorites_del.html', this.$qs.stringify({
fav_id: this.Goodsid,
key: this.Key,
})
).then((res) => {
if (res.data.code == 200) {
this.SearchLoad = false;
this.Is_favorate = false;
this.$vux.toast.show({ text: '取消关注', type: 'text' });
}
})
.catch((err) => {
this.SearchLoad = false;
this.$vux.toast.show({ text: '取消关注失败', type: 'text' });
console.log(err);
})
},

AddCashCard(num) {
var tolnum = this.PayNum + num;
if (tolnum >= 1 && tolnum <= this.TotalNum) {
this.PayNum = this.PayNum + num;
}
console.log(this.PayNum);
},
doShowAddress() {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange(ids, names) {
this.Addressids = ids;
console.log(names);
this.title = '';
for (var i in names) {
this.title = this.title + names[i] + ' ';
}

},

getName(value) {
return value2name(value, ChinaAddressV4Data)
},
logHide(str) {
console.log('on-hide', str)
var areaid = this.Addressids[this.Addressids.length - 1];
if (str) {
this.$axios.get('/goods-calc.html', {
params: {
goods_id: this.Goodsid,
area_id: areaid,
}
}).then((res) => {
if (res.data.code == 200) {
this.Goods_hair_infoobj = res.data.datas;
console.log(this.Goods_hair_infoobj);
}
})
.catch((err) => {
console.log(err);
})
}
},
logShow(str) {
console.log('on-show')
},

AddCart: function () {
if (this.Key == '') {
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.$axios.post('/member_cart-cart_add.html', this.$qs.stringify({
key: this.Key,
goods_id: this.Goodsid,
quantity: this.PayNum,
})
).then((res) => {
if (res.data.code == 200) {
this.GetCartCount();
this.$vux.toast.show({ text: '加入购物车成功', type: 'text' });
} else {
this.$vux.toast.show({ text: '加入购物车失败', type: 'text' });
}
})
.catch((err) => {
this.$vux.toast.show({ text: '加入购物车失败', type: 'text' });
})
},

GetCartCount: function () {
if (this.Key == '') {
return;
}
this.$axios.post('/member_cart-cart_count.html', this.$qs.stringify({
key: this.Key,
})
).then((res) => {
if (res.data.code == 200) {
this.Cart_count = res.data.datas.cart_count;
}
})
.catch((err) => {
console.log(err);
})
},

CheckCommend: function (goodsid) {
this.$router.replace('/gooddetail?goodsid=' + goodsid);
this.Goodsid = goodsid;
this.GetGoodsDetailList();
this.GetCartCount();
},

GoBuy: function () {
this.$router.push({
name: 'firmOrder',
query: {
goodsid: this.Goodsid,
paynum: this.PayNum,
ifcart: 0,
}
});
},
},
}


