import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/views/home/home'
import regionlist from '@/pages/views/home/regionlist'
import goodlist from '@/pages/views/product/goodlist'
import shoplist from '@/pages/views/product/shoplist'
import gooddetail from '@/pages/views/product/gooddetail'
import goodcoupon from '@/pages/views/product/goodcoupon'
import addfeel from '@/pages/views/product/addfeel'
import cusevaluation from '@/pages/views/product/cusevaluation'
import shopcar from '@/pages/views/product/shopcar'
import express from '@/pages/views/product/express'
import firmOrder from '@/pages/views/product/firmorder'
import couponCenter from '@/pages/views/product/couponCenter'
import successpay from '@/pages/views/product/successpay'
import search from '@/pages/views/home/search'
import classfiy from '@/pages/views/home/classfiy'
import barterzone from '@/pages/views/home/barterzone'
import find from '@/pages/views/find/find'
import my from '@/pages/views/my/my'
import followCommodity from '@/pages/views/my/followCommodity'
import goodShop from '@/pages/views/my/goodShop'
import BrowsingHistory from '@/pages/views/my/BrowsingHistory'
import login from '@/pages/views/my/login'
import tbjdOrder from '@/pages/views/my/tbjdOrder'
import shareProfits from '@/pages/views/my/shareProfits'
import orderDetails from '@/pages/views/my/orderDetails'
import applyForSale from '@/pages/views/my/applyForSale'
import applysalesDetail from '@/pages/views/my/applysalesDetail'
import coupon from '@/pages/views/my/coupon'
import register from '@/pages/views/my/register'
import personalSet from '@/pages/views/my/personalSet'
import addressAdministration from '@/pages/views/my/addressAdministration'
import addAddress from '@/pages/views/my/addAddress'
import personalInformation from '@/pages/views/my/personalInformation'
import reviseNickname from '@/pages/views/my/reviseNickname'
import revisePho from '@/pages/views/my/revisePho'
import revisePho_confirm from '@/pages/views/my/revisePho_confirm'
import revisePayPwd from '@/pages/views/my/revisePayPwd'
import about from '@/pages/views/my/about'
import myFeedback from '@/pages/views/my/myFeedback'
import agreement from '@/pages/views/my/agreement'
import returnMoney from '@/pages/views/my/returnMoney'
import returnmoneyDetail from '@/pages/views/my/returnmoneyDetail'
import evaluation from '@/pages/views/my/evaluation'
import singleSupplement from '@/pages/views/my/singleSupplement'
import companyIntroduction from '@/pages/views/my/companyIntroduction'
import singleDetails from '@/pages/views/my/singleDetails'
//import store from '@/pages/views/store/store'



// 我的订单以及五种状态
import myOrder from '@/pages/views/my/myOrder'
// 淘宝京东订单以及四种状态
//import myOrder_tj from '@/pages/views/my/myOrder_tj'



Vue.use(Router)


const router = new Router({

    routes: [{ //首页
            path: '/',
            name: 'home',
            component: home
        },
        { //专区列表
            path: '/regionlist',
            name: 'regionlist',
            component: regionlist
        },

        { //商品列表
            path: '/product/goodlist',
            name: 'goodlist',
            component: goodlist,
            // meta: {
            //     keepAlive: true, //此组件需要被缓存

            // }
        },
        { //店铺列表
            path: '/shoplist',
            name: 'shoplist',
            component: shoplist
        },
        { //商品详情
            path: '/gooddetail',
            name: 'gooddetail',
            component: gooddetail
        },
        { //确认订单可用优惠券
            path: '/goodcoupon',
            name: 'goodcoupon',
            component: goodcoupon
        },
        { //上架心得
            path: '/addfeel',
            name: 'addfeel',
            component: addfeel
        },
        { //全部评价
            path: '/cusevaluation',
            name: 'cusevaluation',
            component: cusevaluation
        },
        { //购物车
            path: '/shopcar',
            name: 'shopcar',
            component: shopcar
        },
        { //物流信息
            path: '/express',
            name: 'express',
            component: express
        },
        { //确认订单
            path: '/firmOrder',
            name: 'firmOrder',
            component: firmOrder
        },
        { //领券中心
            path: '/couponCenter',
            name: 'couponCenter',
            component: couponCenter
        },
        { // 支付成功
            path: '/product/successpay',
            name: 'successpay',
            component: successpay
        },

        { //搜索
            path: '/search',
            name: 'search',
            component: search
        },
        { //分类
            path: '/classfiy',
            name: 'classfiy',
            component: classfiy
        },
        { //易货专区
            path: '/barterzone',
            name: 'barterzone',
            component: barterzone
        },
        { //发现
            path: '/find',
            name: 'find',
            component: find
        },
        { //我的
            path: '/my',
            name: 'my',
            component: my
        },

        //我的订单分类
        {
            path: '/myOrder',
            name: 'myOrder',
            component: myOrder,
        },
        //退货款
        {
            path: '/returnMoney',
            name: 'returnMoney',
            component: returnMoney,
        },
        //去评价
        {
            path: '/evaluation',
            name: 'evaluation',
            component: evaluation,
        },
        //退货款详情
        {
            path: '/returnmoneyDetail',
            name: 'returnmoneyDetail',
            component: returnmoneyDetail,
        },
        //退单详情
        {
            path: '/singleDetails',
            name: 'singleDetails',
            component: singleDetails,

        },
        //我的订单详情
        {
            path: '/orderDetails',
            name: 'orderDetails',
            component: orderDetails,
        },
        //关注商品
        {
            path: '/followCommodity',
            name: 'followCommodity',
            component: followCommodity
        },
        //关注店铺
        {
            path: '/goodShop',
            name: 'goodShop',
            component: goodShop
        },
        //浏览记录
        {
            path: '/BrowsingHistory',
            name: 'BrowsingHistory',
            component: BrowsingHistory
        },
        //登录
        {
            path: '/login',
            name: 'login',
            component: login
        },
        //注册
        {
            path: '/register',
            name: 'register',
            component: register
        },
        //设置
        {
            path: '/personalSet',
            name: 'personalSet',
            component: personalSet
        },
        //个人信息
        {
            path: '/personalInformation',
            name: 'personalInformation',
            component: personalInformation
        },
        //地址管理
        {
            path: '/addressAdministration',
            name: 'addressAdministration',
            component: addressAdministration
        },
        //新增/编辑地址
        {
            path: '/addAddress',
            name: 'addAddress',
            component: addAddress
        },
        //修改昵称
        {
            path: '/reviseNickname',
            name: 'reviseNickname',
            component: reviseNickname
        },
        //修改手机验证
        {
            path: '/revisePho',
            name: 'revisePho',
            component: revisePho
        },
        //修改手机验证下一步
        {
            path: '/revisePho_confirm',
            name: 'revisePho_confirm',
            component: revisePho_confirm
        },

        //设置支付密码
        {
            path: '/revisePayPwd',
            name: 'revisePayPwd',
            component: revisePayPwd
        },
        //关于
        {
            path: '/about',
            name: 'about',
            component: about
        },
        //公司简介
        {
            path: '/companyIntroduction',
            name: 'companyIntroduction',
            component: companyIntroduction
        },
        //用户协议
        {
            path: '/agreement',
            name: 'agreement',
            component: agreement
        },
        //我的反馈
        {
            path: '/myFeedback',
            name: 'myFeedback',
            component: myFeedback
        },
        //淘宝/京东订单
        {
            path: '/tbjdOrder',
            name: 'tbjdOrder',
            component: tbjdOrder
        },
        //淘宝补单
        {
            path: '/singleSupplement',
            name: 'singleSupplement',
            component: singleSupplement
        },

        //分享赚收益
        {
            path: '/shareProfits',
            name: 'shareProfits',
            component: shareProfits
        },
        //申请售后
        {
            path: '/applyForSale',
            name: 'applyForSale',
            component: applyForSale
        },
        //申请售后操作详情
        {
            path: '/applysalesDetail',
            name: 'applysalesDetail',
            component: applysalesDetail
        },
        //我的优惠券
        {
            path: '/coupon',
            name: 'coupon',
            component: coupon
        },
        //店铺
        // {
        //     path: '/store',
        //     name: 'store',
        //     component: store
        // },

    ]
})


router.beforeEach((to, from, next) => {
    // to: Route: 即将要进入的目标 路由对象
    // from: Route: 当前导航正要离开的路由
    // next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
    // A跳转到B，B页面停留在A页面的滚动位置；解决方法：将scrollTop设置为0
    document.body.scrollTop = 0
        // Firefox
    document.documentElement.scrollTop = 0
        //     // Safari
    window.pageYOffset = 0
    next();
});

export default router


