
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Scroll
},
name: 'addfeel',

data() {
return {
Feel_list: [], //评价列表
Leave_list: [], //点赞列表
Feeldetail_info: [], //上架理由
SearchLoad: false,
Pushid: this.$route.query.pushid, //推荐ID
Key: this.getCookie("key"), //用户唯一标识
show: false, //是否查看更多
FeelContent: '', //上架理由
FeelContentType: false, //是否显示评论框
}
},

created: function() {
if (this.Key == '') {
this.$router.push('/login');
}
},

created: function() {
this.GetFeelList();
},

mounted: function() {},

methods: {

countshow() {
if (this.FeelContentType) {
this.FeelContentType = false;
} else {
this.FeelContentType = true;
}
},

GetFeelDetailList: function() {
if (this.Key == '') {
return;
}
this.$axios.post('/goods-goodsMessage.html', this.$qs.stringify({
goods_commonid: this.Feeldetail_info.goods_commonid,
type: '',
key: this.Key,
content: '',
})).then((res) => {
if (res.data.code == 200) {
this.Feel_list = res.data.datas;
console.log(res.data.datas);
}
})
.catch((err) => {
console.log(err);
})
},

GetFeelList: function() {
this.SearchLoad = true;
this.$axios.post('/goods-pushReason.html', this.$qs.stringify({
push_id: this.Pushid,
})).then((res) => {
if (res.data.code == 200) {
this.Feeldetail_info = res.data.datas;
console.log(this.Feeldetail_info);
this.GetFeelDetailList();
this.GetLeaveList();
this.SearchLoad = false;
}
})
.catch((err) => {
console.log(err);
this.SearchLoad = false;
})
},

GetLeaveList: function() {
if (this.Key == '') {
return;
}
this.$axios.post('/goods-leave_up.html', this.$qs.stringify({
key: this.Key,
type: '',
goods_commonid: this.Feeldetail_info.goods_commonid,
store_id: this.Feeldetail_info.store_id,
})).then((res) => {
if (res.data.code == 200) {
this.Leave_list = res.data.datas;
console.log(this.Leave_list);
}
})
.catch((err) => {
console.log(err);
})
},

AddFeelDetail: function() {
if (this.Key == '') {
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.SearchLoad = true;
this.$axios.post('/goods-goodsMessage.html', this.$qs.stringify({
goods_commonid: this.Feeldetail_info.goods_commonid,
type: 1,
key: this.Key,
content: this.FeelContent,
})).then((res) => {
if (res.data.code == 200) {
this.FeelContent = '';
this.Feel_list = res.data.datas;
console.log(res.data.datas);
this.SearchLoad = false;
this.FeelContentType = false;
}
})
.catch((err) => {
this.SearchLoad = false;
this.FeelContentType = false;
console.log(err);
})
},

AddLeave: function() {
if (this.Key == '') {
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.SearchLoad = true;
this.$axios.post('/goods-leave_up.html', this.$qs.stringify({
key: this.Key,
type: 1,
goods_commonid: this.Feeldetail_info.goods_commonid,
store_id: this.Feeldetail_info.store_id,
})).then((res) => {
if (res.data.code == 200) {
this.Leave_list = res.data.datas;
this.SearchLoad = false;
}
})
.catch((err) => {
console.log(err);
})
},
},
}


