import Vue from 'vue';
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XInput,
XButton,
Group,
XHeader
},
data() {
return {
orderid: '',//订单编号
picName:"",//接口返回图片名称
picUrl:"",//接口返回图片地址
filename:'',//上传的图片名
Key:this.getCookie("key"),
}
},
name:'singleSupplements',

created: function () {
if (this.Key == '') {
this.$router.push('/login');
}
},
methods: {

fileChanged() {
const singleFile = this.$refs.file.files[0];
console.log(singleFile);
this.filename = singleFile.name
var formdata1=new FormData();
formdata1.append('key',this.Key);
formdata1.append('order_pic',singleFile,singleFile.name);
let config = {headers:{'Content-Type':'multipart/form-data'}};
this.$axios.post("/member_order-upload_pic.html", formdata1,config).then((res) => {
if (res.data.code == 200) {
this.picName=res.data.datas.file_name;
this.picUrl=res.data.datas.pic;
} else {
this.$vux.alert.show({
title: '失败',
content: res.data.error
})
}
})
.catch((err) => {
console.log(err);
})
},

subm(){
this.$axios.post("/member_order-rep_order_post.html", this.$qs.stringify({
key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
type: "tbk",
order_id:this.orderid,
order_pic:this.picName
})
).then((res) => {
if (res.data.code == 200) {
this.$vux.alert.show({
title: '成功',
content:"提交成功"
})
} else {
this.$vux.alert.show({
title: '失败',
content: res.data.datas.error
})
}
})
.catch((err) => {
console.log(err);
})
}
}
}


