import Vue from 'vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
XHeader,
Group,
Cell
},
data() {
return {
username: this.getCookie("username"),
avatar: this.getCookie("avatar"),
mobile: '',
Key: this.getCookie("key"),
}
},
name: 'personalInformation',

created: function () {
if (this.Key == '') {
this.$router.push('/login');
}
},
mounted: function ($routey) {
this.$axios.get('/member_account-get_mobile_info.html', {
params: { key: this.Key }
}).then((res) => {
if (res.data.code == 200) {
this.mobile = res.data.datas.mobile;
}
}).catch((err) => {
console.log(err);
})
},
methods: {

html5Reader(file) {
const reader = new FileReader()
reader.onload = (e) => {
console.log(e.target.result);
this.$axios.post("/member_address-uploadAvt.html", this.$qs.stringify({
key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
pic: e.target.result
})
).then((res) => {
if (res.data.code == 200) {
this.avatar = res.data.path;
this.setCookie("avatar", this.avatar);
} else {
this.$vux.alert.show({
title: '失败',
content: res.data.error
})
}
})
.catch((err) => {
console.log(err);
})
}
reader.readAsDataURL(file);
},

fileChanged() {
console.log("图片选择");
const singleFile = this.$refs.file.files[0];
this.html5Reader(singleFile);
},

changedPic() {
this.$refs.file.click();
},
GotomodifyName() {
this.$router.push({ name: 'reviseNickname' });
},
GotomodifyPhone(value) {
this.$router.push({ name: 'revisePho', query: { type: value } });
}
},

}


