export default {
    name: "couponCenter",
    data() {
        return {
            Key: this.getCookie("key"),
            //用户唯一标识
            // Key: '3fc96063e73705021471b7548a8cb675',
            // Key: "6ca9f3434c210705c756126cd5bb6d78",
            //Key: "207144cf80061938e9e191b47af75b72",
            //id: this.$route.query.order_id,
            store_info: "",
            voucher_list: "",
            gooddetail: "",
            goods_info: "",
            goodimg: "",
            goodid: this.$route.query.goods_id,
            storeid: this.$route.query.store_id,
        };
    },
    created() {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        this.getcoupondetail();
        this.getgoodetail()
    },
    filters: {
        //时间戳转化为时间格式
        timestampToTime(timestamp) {
            var date = new Date(timestamp * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
            var Y = date.getFullYear() + ".";
            var M =
                (date.getMonth() + 1 < 10 ?
                    "0" + (date.getMonth() + 1) :
                    date.getMonth() + 1) + ".";
            var D = date.getDate() + " ";
            // var h = date.getHours() + ":";
            // var m = date.getMinutes() + ":";
            // var s = date.getSeconds();
            return Y + M + D;
        }
    },
    methods: {
        //获取优惠券信息
        getcoupondetail() {
            this.$axios.post('/voucher-voucher_tpl_list.html', this.$qs.stringify({
                    key: this.Key,
                    store_id: this.storeid,
                    gettype: 'free',
                    goods_id: this.goodid,
                })).then((res) => {
                    if (res.data.code == 200) {
                        this.store_info = res.data.datas.store_info
                        this.voucher_list = res.data.datas.voucher_list[0]
                    }
                })
                .catch((err) => {

                    console.log(err);
                })
        },
        //获取商品信息
        getgoodetail() {
            this.$axios.post('/goods-get_goods_basic_info.html', this.$qs.stringify({
                    key: this.Key,
                    store_id: this.storeid,
                    goods_id: this.goodid,
                })).then((res) => {
                    //console.log(res)
                    if (res.data.code == 200) {
                        this.goods_info = res.data.datas.goods_info
                        this.goodimg = res.data.datas.goods_image_mobile[0]
                    }
                })
                .catch((err) => {

                    console.log(err);
                })
        },

        //领取优惠券
        getvoucher(id) {
            this.$axios.post('/member_voucher-voucher_freeex.html', this.$qs.stringify({
                    key: this.Key,
                    tid: id, //优惠券id
                })).then((res) => {
                    console.log(res)
                    if (res.data.code == 200) {
                        this.$vux.toast.show({ text: '领取成功', type: 'text' });
                        setTimeout(() => {
                            this.$router.push({
                                name: 'gooddetail',
                                query: {
                                    goodsid: this.goodid,
                                }
                            });
                        }, 2000);

                    } else {
                        this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
                    }
                })
                .catch((err) => {

                    console.log(err);
                })
        }
    }
}


