//我的页面
import Vue from 'vue';
import { Cell, Group, Grid, GridItem, Flexbox, Badge, FlexboxItem } from 'vux'
import bottom from "./../../views/home/bottom.vue";
export default {
    data() {
        return {
            state: 0,
        }
    },
    components: {
        Group,
        bottom,
        Cell,
        Grid,
        GridItem,
        Flexbox,
        Badge,
        FlexboxItem
    },
    data() {
        return {
            Member_list: [], //用户信息
            Key: this.getCookie("key"), //用户唯一标识
            User_name: '', //	用户名
            Avatar: '', //	头像
            Member_level: 0, //	用户等级
            Level_name: '', //	等级信息
            Favorites_store: 0, //	收藏店铺数
            Favorites_goods: 0, //	收藏商品数
            Cart_count: 0, //	购物车数量
            Browse_count: 0, //	浏览记录数量
            mid: '', //用户id
            show_level:true//
            // Order_nopay_count: 0,//	待付款订单数
            // Order_noreceipt_count: 0,//	待收货订单数
            // Order_notakes_count: 0,//	待发货订单数
            // Order_noeval_count: 0,//	待评价订单数
            // Return: 0,//	退货退款数
            // Point: 0,//	会员积分
            // Predepoit: 0,//	预存款可用金额
            // Voucher: 0,//	优惠券数
        }
    },
    //页面载入
    created: function() {
        // this.setCookie("key", "6c921998c5128a47e9ac436dc7f95b4f", 15);
        console.log(this.Key);
        if (this.Key != '') {
            this.GetUserInfo();
            this.GetCartCount();
            this.GetGoodbrowseCount();
        }
    },
    mounted() {

    },
    filters:{
        level(value){
            console.log(value)
            if(value == 0){
            }else if(value == 1){
                return '../../../assets/level_1.png'
            }else if(value == 2){
                return '../../../assets/level_2.png'
            }else if(value == '3'){
                return '../../../assets/level_3.png'
            }else if(value == '4'){
                return '../../../assets/level_4.png'
            }else if(value == '5'){
                return '../../../assets/level_5.png'
            }else if(value == '6'){
                return '../../../assets/level_6.png'
            }else{
                return '../../../assets/level_7.png'
            }
        }
    },
    methods: {
        //获取用户信息
        GetUserInfo: function() {
            this.$axios.post('/member_index-index.html',
                    this.$qs.stringify({
                        key: this.Key,
                    })
                ).then((res) => {
                    if (res.data.code == 200) {
                        this.Member_list = res.data.datas.member_info;
                        this.User_name = this.Member_list.user_name; //	用户名
                        this.Avatar = this.Member_list.avatar; //	头像
                        this.setCookie("avatar", this.Member_list.avatar),
                        this.Member_level = this.Member_list.member_level; //	用户等级
                        if(this.Member_level == 0){
                            this.show_level = false;
                        }else{
                            this.show_level = true;
                        }
                        this.setCookie("member_level", this.Member_list.member_level),
                        this.Level_name = this.Member_list.level_name; //	等级信息
                        this.Favorites_store = this.Member_list.favorites_store; //	收藏店铺数
                        this.Favorites_goods = this.Member_list.favorites_goods; //	收藏商品数

                        this.mid = this.Member_list.member_id; //用户id
                        console.log(this.Member_list);

                    }
                })
                .catch((err) => {
                    console.log(err);
                })
        },
        //获取购物车数量
        GetCartCount: function() {
            this.$axios.get('/member_cart-cart_count.html', {
                    params: {
                        key: this.Key,
                    }
                }).then((res) => {
                    if (res.data.code == 200) {
                        this.Cart_count = res.data.datas.cart_count;
                        console.log(this.Cart_count);
                    }
                })
                .catch((err) => {
                    console.log(err);
                })
        },
        //获取浏览记录
        GetGoodbrowseCount: function() {
            this.$axios
                .get('/member_goodsbrowse-browse_list.html', {
                    params: {
                        key: this.Key,
                        curpage: 1, //页码
                        page: 1, //一页商品数量
                    }
                }).then((res) => {
                    if (res.data.code == 200) {
                        this.Browse_count = res.data.page_total;
                    }
                })
                .catch((err) => {
                    console.log(err);
                })
        },

        GotomyOrder() {
            this.$router.push({
                name: 'myOrder',
            });
        },
        GotofollowCommodity() {
            this.$router.push({
                path: '/followCommodity',
            });
        },
        GotogoodShop() {
            this.$router.push({
                name: 'goodShop',
            });
        },
        GotoBrowsingHistory() {
            this.$router.push({
                name: 'BrowsingHistory',
            });
        },
        login() {
            this.$router.push({
                name: 'login',
            });
        },
        GototbjdOrder() {
            this.$router.push({
                name: 'tbjdOrder',
            });
        },
        GotoshareProfits() {
            this.$router.push({
                name: 'shareProfits',
                query: { mid: this.mid }
            });
        },
        Gotosetting() {
            this.$router.push({
                name: 'personalSet',
            });
        },
        jumpTomyorder(value) {
            this.$router.push({ name: 'myOrder', params: { state: value } });
        },
        Gotocoupon() {
            this.$router.push({ name: 'coupon' });
        },
        Gotocustomer() {
            window.location.href = "http://chat56.live800.com/live800/chatClient/chatbox.jsp?companyID=880789&configID=137108&jid=7677352762";
        },
        jumpToreturn() {
            this.$router.push({
                name: 'returnMoney',
            });
        },

    },
}


