

import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'qs'
import ApiUrl from "./common/js/api.js";
import Dom from "./common/js/dom.js";
import Cookie from "./common/js/cookie.js";
import Filters from "./common/js/filters.js";
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


var instance = axios.create({
baseURL: ApiUrl.root,
timeout: 8000,
headers: {








},
withCredentials: true
});

Vue.prototype.$axios = instance;
Vue.prototype.$qs = qs;
Vue.prototype.$globalScope = ApiUrl;
Vue.config.productionTip = false;
Vue.use(Vuex);
Vue.use(ApiUrl);
Vue.use(Dom);
Vue.use(Cookie);
Vue.use(Filters);
Vue.use(AlertPlugin);
Vue.use(ToastPlugin);
Vue.use(ConfirmPlugin);
/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})


