import Vue from 'vue';
import { XInput, Group, XButton, Checker, XHeader } from 'vux';
export default {
  components: {
    XInput,
    XButton,
    Group,
    XHeader,
    Checker
  },
  name: 'register',
  data() {
    return {
      pho: '',//手机号码
      img_code: '',//图形验证码
      pho_code: '',//手机验证码
      agree: true,//是否同意注册协议
      code_text: "获取验证码",
      img_code_url_k: '',//获取图形验证码图片地址url的key
      img_code_url: null,//图形验证码图片地址url
      err: '',
      time: 0,//倒计时时间
      ivcode: '',//邀请人id
      //SSOID: '',
      but_style: {
        background: '#f16565',
        color: '#fff'
      }
    }
  },
  //数据初始化
  mounted: function () {
    this.ivcode = this.$route.query.ivcode;
    //this.SSOID = this.getQueryString("SSOID");
    this.get_pic_code();
  },
  methods: {
    //获取图形验证码图片地址
    get_pic_code() {
      this.$axios.get('/seccode-makecodekey.html', {}).then((res) => {
        if (res.data.code == "200") {
          this.img_code_url_k = res.data.datas.codekey;
          this.img_code_url = this.$globalScope.root + '/seccode-makecode.html?k=' + res.data.datas.codekey + '&t=' + Math.random();
        }
      }).catch((err) => {
        console.log(err);
      })
    },
    //点击图形验证码
    img_code_cli() {
      this.get_pic_code();
    },
    //手机验证码获取
    code_text_cli() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        this.$axios.get('/connect-get_sms_captcha.html', {
          params: { "type": 1, key: '', "sec_val": this.img_code, "sec_key": this.img_code_url_k, "phone": this.pho }
        }).then((res) => {
          if (res.data.state) {
            //倒计时显示
            this.but_style.background = "#ccc";
            this.$vux.alert.show({
              title: '成功',
              content: '短信验证码发送成功'
            })
            clearInterval(this.interval);
            this.time = res.data.sms_time;
            this.code_text = this.time + "秒后重试";
            this.interval = setInterval(() => {
              if (this.time > 0) {
                this.time--
                this.code_text = this.time + "秒后重试";
              } else {
                this.code_text = "获取验证码";
                this.but_style.background = "#f16565";
                clearInterval(this.interval);
              }
            }, 1000)
          }
          else {
            this.$vux.alert.show({
              title: '失败',
              content: res.data.msg
            })
          }
        }).catch((err) => {
          console.log(err);
        })
      }
      else {
        this.$vux.alert.show({
          title: '失败',
          content: '手机号码格式错误'
        })
      }
    },
    //立即注册
    register_subm() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        if (this.img_code != '' && this.img_code) {
          if (this.pho_code != '' && this.pho_code) {
            if (this.agree) {
              this.$axios.get('/connect-check_sms_captcha_login.html', {
                params: { "type": 1, "captcha": this.pho_code, "ivcode": this.ivcode, "phone": this.pho }
              }).then((res) => {
                if (res.data.code == "200") {
                  this.setCookie("key", res.data.datas.key);
                  this.setCookie("username", res.data.datas.username);
                  this.setCookie("userid", res.data.datas.userid);
                  this.$router.push('/my');
                }
                else {
                  this.$vux.alert.show({
                    title: '注册失败',
                    content: res.data.datas.error
                  })
                }
              }).catch((err) => {
                console.log(err);
              })
            } else {
              this.$vux.alert.show({
                title: '失败',
                content: '请同意注册协议'
              })
            }
          } else {
            this.$vux.alert.show({
              title: '失败',
              content: '手机验证码格式不正确'
            })
          }
        } else {
          this.$vux.alert.show({
            title: '失败',
            content: '图片验证码格式不正确'
          })
        }
      } else {
        this.$vux.alert.show({
          title: '失败',
          content: '手机号码格式错误'
        })
      }
    },
    //微信登录
    wx_login() {
      location.href = this.$globalScope.root + '/connect-index.html';
    }
  }
}


