import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Cell, Group, Popup} from 'vux'
import { SSL_OP_NETSCAPE_REUSE_CIPHER_CHANGE_BUG } from 'constants';

export default {
    data () {
        return {
            demo2: '全部',
            Key: this.getCookie("key"),//用户唯一标识
            //Key:'6c921998c5128a47e9ac436dc7f95b4f',
            index: 0,
            state_now:0,
            eventId:0,
            state_desc:'',//订单详情状态
            state_order:'',//订单状态
            reciver_name:'',//收获名
            reciver_phone:'',//说货人手机号码
            reciver_addr:'',//收货地址
            store_name:'',//商店名
            goods_spec:'',//规格
            goods_price:'',//商品单价
            goods_num:'',//数量
            goods_amount:0,//商品总价
            shipping_fee:'',//运费
            order_amount:'',//订单总价
            fc_score:'',//积分消费
            fc_ticket:'',//电子券抵用
            real_pay_amount:'',//实际付款
            order_sn:'',//订单编号
            add_time:'',//创建时间
            payment_time:'',//支付时间
            payment_name:'',//支付方式
            invoice:'',//发票信息
            order_message:'',//买家留言
            preferential:'',//优惠抵扣
            logistics:"",
            ifFinnished:true,//付款时间
            ifDelivery:false,//发货时间
            ifClinch:false,//成交时间
            order_id:0,
            PayShow: false,//是否显示支付类型
            PayType: 0,//支付类型
            PayText: '中国工商银行',
            Pay_sn: this.$route.query.pay_sn,//支付单号
            Paylist: [],//支付方式
            goods_list:[],//订单详情商品列表
            showCancelBtn:true,//取消订单
            showCustomerBtn:true,//联系客服
            showPayBtn:true,//去支付
            showEvaluatBtn:true,//去评价
            showAftergoodBtn:true,//申请售后
            showDeleteorderBtn:true,//删除订单
            showRefundBtn:true,//订单退款
            showLogisticsBtn:true,//查看物流
            showGoodsBtn:true,//确认收货
            voucher:'',
            if_disabled:false,//是否柯点击
        }
  },
  components: {
    XHeader,
    Tab,
    Cell,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Group,
    Popup
  },
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted(){
    this.state_now = this.$route.params.state;
    console.log(this.$route.query.order_id)
    this.order_id = this.$route.query.order_id;
    //进入页面后获取数据


    this.latestLogistics();
    this.orderDetailMsg();
  },
  methods: {
    jumpTocodingdetail(){
      this.$router.push({ name: 'orderDetails'});
    },
    jumpTosales(value){
      this.$router.push({ name: 'applyForSale', query:{order_id:this.order_id,order_state:value} });
    },
    jumpToevaluation(){
      this.$router.push({ name: 'evaluation'});
    },
    jumpTologist(){
      this.$router.push({ name: 'express', query:{order_id:this.order_id} });
    },
    //复制文本
    copyText() {
      var Url2=document.getElementById("bianhao");
      console.log(Url2)
      Url2.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$vux.toast.show({ text: '复制成功', type: 'text' });
    },
    //最新物流信息
    latestLogistics: function () {
      this.$axios.get('/member_order-get_current_deliver.html',{params:{
        order_id: this.order_id,
        key: this.Key,
      }}
      ).then((res) => {
        if (res.data.code == 200) {
          this.logistics = res.data.datas.deliver_info.context;
          
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },
    //确认收货
    confirmGoods(){
      this.$axios.post('/member_order-order_receive.html', this.$qs.stringify({
        order_id: this.order_id	,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.$vux.alert.show({ content: '确认收货成功' });
        }
      })
      .catch((err) => {
          console.log(err);
          this.$vux.alert.show({ content: err.data.datas.error });
      })
    },
    //订单详情信息
    orderDetailMsg: function () {
      this.$axios.get('/member_order-order_info.html',{params:{
        order_id: this.order_id,
        key: this.Key,
      }}
      ).then((res) => {
        if (res.data.code == 200) {
          let order_info = res.data.datas.order_info;
          this.state_desc = order_info.state_desc;
          if(this.state_desc == '待发货'){
             this.state_desc = '等待商家发货中...'
             this.showCancelBtn = false;
             this.showPayBtn = false;
             this.showCustomerBtn = true;
             this.showEvaluatBtn = false;
             this.showAftergoodBtn = false;
             this.showDeleteorderBtn = false;
             this.showLogisticsBtn = false;
             this.showGoodsBtn = false;
             this.showRefundBtn = true;

          }else if(this.state_desc == '待付款'){
             this.state_desc = '等待您付款...'
             this.showCancelBtn = true;
             this.showPayBtn = true;
             this.showCustomerBtn = true;
             this.showEvaluatBtn = false;
             this.showRefundBtn = false;
             this.showAftergoodBtn = false;
             this.showDeleteorderBtn = false;
             this.showLogisticsBtn = false;
             this.showGoodsBtn = false;
          }else if(this.state_desc == '待收货'){
             this.state_desc = '商品已发出...'
             this.showCancelBtn = false;
             this.showPayBtn = false;
             this.showCustomerBtn = false;
             this.showEvaluatBtn = false;
             this.showRefundBtn = false;
             this.showAftergoodBtn = true;
             this.showDeleteorderBtn = false;
             this.showLogisticsBtn = true;
             this.showGoodsBtn = true;
             this.ifDelivery = true;
          }else if(this.state_desc == '待评价'){
             this.state_desc = '本次交易已完成...'
             this.showCancelBtn = false;
             this.showPayBtn = false;
             this.showCustomerBtn = false;
             this.showEvaluatBtn = true;
             this.showRefundBtn = false;
             this.showAftergoodBtn = true;
             this.showDeleteorderBtn = true;
             this.showLogisticsBtn = false;
             this.showGoodsBtn = false;
             this.ifClinch = true;
          }else if(this.state_desc == '已取消'){
            this.state_desc = '交易已关闭...'
         }

          this.reciver_name = order_info.reciver_name;
          this.reciver_phone = order_info.reciver_phone;
          this.reciver_addr = order_info.reciver_addr;
          this.store_name = order_info.store_name;
          this.goods_list = order_info.goods_list;
          this.add_time = order_info.add_time;
          if(order_info.if_lock == true){
            this.if_disabled = false;
          }else{
            this.if_disabled = true;
          }

          this.goods_list.forEach(item => {
                this.goods_amount = this.goods_amount + item.goods_price * item.goods_num;
        })
          
          this.shipping_fee = order_info.shipping_fee
          this.order_amount = order_info.order_amount;
          this.fc_score = order_info.fc_score
          this.fc_ticket = order_info.fc_ticket;
          this.real_pay_amount = order_info.real_pay_amount
          this.order_sn = order_info.order_sn;
          this.payment_time = order_info.payment_time;
          this.payment_name = order_info.payment_name;
          this.invoice = order_info.invoice
          this.voucher = order_info.voucher
          if(order_info.voucher == null){
            this.voucher = 0;
          }
          this.order_message = order_info.order_message;
          
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },
     //确认支付方式
    SetPayType:function(){
      this.PayShow = false;
     // console.log(this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '')
      window.location.href = this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '';
     },
     //选择支付方式
     ChangePayType: function (paytype) {
      this.PayType = paytype;
      // this.$vux.toast.show({ text: '你选择了' + paytype, type: 'text' });

      // window.location.href = this.$globalScope.root + '/member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '';
     },
     //点击去支付显示支付弹框
     ShowPay: function () {
      this.$axios.post('/member_buy-pay.html', this.$qs.stringify({
          key: this.Key,//用户唯一标识
          pay_sn: this.Pay_sn,//订单号
      })).then((res) => {
          if (res.data.code == 200) {
              console.log(res.data.datas);
              this.Paylist = res.data.datas.pay_info.payment_list;
              this.PayType = this.Paylist[0].payment_code;
              this.PayShow = true;
              // this.$vux.alert.show({ content: '订单提交成功，订单号：' + res.data.datas.pay_sn });
          } else {
              this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
          }
      })
          .catch((err) => {
              console.log(err);
          })
    },
    //取消订单
    cancelOrder(){
      this.$axios.post('/member_order-order_cancel.html', this.$qs.stringify({
        order_id: this.order_id,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.$vux.alert.show({ content: '取消订单成功' });
          setTimeout(() => {
            this.$router.go(-1);
          }, 2000);   
        }
      })
      .catch((err) => {
          console.log(err);
          this.$vux.alert.show({ content: err.data.datas.error });
      })
    },
  },
}














