export default {
    name: "shopCar",
    data() {
        return {
            shopcarList: [],
            delshow: false,
            emptyimgshow: false,
            edishow: false,
            choseall: true,
            //Key: '6c921998c5128a47e9ac436dc7f95b4f',
            //用户唯一标识
            // Key: '3fc96063e73705021471b7548a8cb675',
            //Key: "6ca9f3434c210705c756126cd5bb6d78",
            //Key: "207144cf80061938e9e191b47af75b72",
            //Key: "1b238d9e8e59687322e91c9150048dba",
            Key: this.getCookie("key"),
            sum: 0,
        };
    },
    filters: {
        keepTow(val) {
            var value = Number(val);
            return value.toFixed(2);
        }
    },
    created() {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        this.getshopcarlist();

    },
    mounted() {

    },
    // computed: {
    //     chooseList() {
    //         let chooseList = [];
    //         this.shopcarList.forEach(item => {
    //             if (item.chooseStatus) {
    //                 chooseList.push(item);
    //             }
    //         });
    //         return chooseList;
    //     },

    //     totalNum() {
    //         let num = 0;
    //         this.shopcarList.forEach(item => {
    //             if (item.isChoose) {
    //                 num += item.salesPrice * item.qty;
    //             }
    //         });
    //         return num;
    //     }
    // },

    methods: {

        offedishow() {
            this.delshow = false
            this.getallprice()
        },

        //计算凑单
        getreduce(index) {
            let asum = 0;
            for (let k = 0; k < this.shopcarList[index].goods.length; k++) {
                if (this.shopcarList[index].goods[k].ischose || this.shopcarList[index].goods[k].ischose === undefined) {
                    asum += (this.shopcarList[index].goods[k].goods_price * this.shopcarList[index].goods[k].goods_num)
                }
            }

            let reduce = ",还差" + (this.shopcarList[index].free_freight - asum) + "元"
            if (asum < this.shopcarList[index].free_freight) {
                return reduce
            } else {
                return ""
            }
        },
        //获取购物车 购物车信息
        getshopcarlist() {
            console.log(this.Key)
            this.$axios
                .post(
                    // "/member_cart-cart_count.html",
                    "/member_cart-cart_list.html",
                    this.$qs.stringify({
                        key: this.Key,
                    })
                )
                .then(res => {
                    console.log(res)
                    this.sum = res.data.datas.sum
                    this.shopcarList = res.data.datas.cart_list
                    for (let i = 0; i < this.shopcarList.length; i++) {


                        this.$set(this.shopcarList[i], "ischose", true);
                        this.$set(this.shopcarList[i], "isshow", true);
                        this.$set(this.shopcarList[i], "reduce", this.getreduce(i));
                        for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                            this.$set(this.shopcarList[i].goods[k], "ischose", true);
                            this.$set(this.shopcarList[i].goods[k], "isshow", true);
                        }

                    }

                    if (this.shopcarList.length == 0) {
                        this.emptyimgshow = true;
                    } else {
                        this.emptyimgshow = false;
                    }

                })
                .catch(err => {
                    this.emptyimgshow = true;
                    console.log(err)
                });
        },



        // getfirstreduc() {
        //     for (let i = 0; i < this.shopcarList.length; i++) {



        //         this.$set(this.shopcarList[i], "reduce", this.getreduce(i));

        //     }
        // },


        //购物车操作
        //选择全部商品
        choseallgood() {
            if (this.choseall) {
                this.choseall = false;
                for (let i = 0; i < this.shopcarList.length; i++) {
                    this.$set(this.shopcarList[i], "ischose", false);
                    for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                        this.$set(this.shopcarList[i].goods[k], "ischose", false);
                    }
                }

            } else {
                this.choseall = true;
                for (let i = 0; i < this.shopcarList.length; i++) {
                    this.$set(this.shopcarList[i], "ischose", true);
                    for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                        this.$set(this.shopcarList[i].goods[k], "ischose", true);
                    }
                }
            }
            this.getallprice()

        },
        //选择店铺全部商品
        choseshopall(index) {
            if (this.shopcarList[index].ischose) {
                this.$set(this.shopcarList[index], "ischose", false);
                for (let k = 0; k < this.shopcarList[index].goods.length; k++) {
                    this.$set(this.shopcarList[index].goods[k], "ischose", false);
                }
                this.choseall = false;

            } else {
                this.$set(this.shopcarList[index], "ischose", true);
                for (let k = 0; k < this.shopcarList[index].goods.length; k++) {
                    this.$set(this.shopcarList[index].goods[k], "ischose", true);
                }
                let falg = true
                for (var i = 0; i < this.shopcarList.length; i++) {
                    if (this.shopcarList[i].ischose == false) {
                        falg = false
                        break
                    }
                }
                this.choseall = falg;

            }
            this.getallprice()
        },
        //选择单个商品
        choseone(index, i) {
            if (this.shopcarList[index].goods[i].ischose) {
                this.$set(this.shopcarList[index].goods[i], "ischose", false);
                this.$set(this.shopcarList[index], "ischose", false);
                this.choseall = false;
            } else {
                this.$set(this.shopcarList[index].goods[i], "ischose", true);
                let falg = true
                for (let k = 0; k < this.shopcarList[index].goods.length; k++) {
                    if (this.shopcarList[index].goods[k].ischose == false) {
                        falg = false
                        break
                    }
                }
                this.$set(this.shopcarList[index], "ischose", falg);
                if (falg) {
                    let falg2 = true
                    for (var i = 0; i < this.shopcarList.length; i++) {
                        if (this.shopcarList[i].ischose == false) {
                            falg2 = false
                            break
                        }
                    }
                    this.choseall = falg2;
                }
            }
            this.getallprice()
        },



        //购物车减少
        reduceCartNum(id, num, qty, index, i) {
            if (num === 1) {
                return;
            } else {
                num--;

                this.$axios.post('/member_cart-cart_edit_quantity.html', this.$qs.stringify({
                        key: this.Key,
                        cart_id: id,
                        quantity: num,
                    })).then((res) => {
                        // console.log(res)
                        if (res.data.code == 200) {
                            console.log(index);
                            // console.log(this.shopcarList[index]);
                            this.$set(this.shopcarList[index].goods[i], "goods_num", num);
                            this.getallprice();
                            this.$set(this.shopcarList[index], "reduce", this.getreduce(index));
                        }
                    })
                    .catch((err) => {

                        console.log(err);
                    })

            }

        },
        //购物车增加
        addCartNum(id, num, qty, index, i) {
            if (qty === num) {
                this.$vux.toast.show({ text: '商品库存不足', type: 'text' });
                return;
            } else {
                num++;

                this.$axios.post('/member_cart-cart_edit_quantity.html', this.$qs.stringify({
                        key: this.Key,
                        cart_id: id,
                        quantity: num,
                    })).then((res) => {
                        // console.log(res)
                        if (res.data.code == 200) {
                            // console.log(this.shopcarList[index]);
                            this.$set(this.shopcarList[index].goods[i], "goods_num", num);
                            this.getallprice()
                            this.$set(this.shopcarList[index], "reduce", this.getreduce(index));
                        }
                    })
                    .catch((err) => {

                        console.log(err);
                    })



            }

        },
        //购物车删除
        removeCartNum() {
            let carid = []
            for (let i = 0; i < this.shopcarList.length; i++) {
                for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                    if (this.shopcarList[i].goods[k].ischose) {
                        carid.push(this.shopcarList[i].goods[k].cart_id)
                    }
                }
            }
            let carids = carid.join(',')

            console.log(carids)
            this.$axios.post('/member_cart-cart_del.html', this.$qs.stringify({
                    key: this.Key,
                    cart_id: carids,
                })).then((res) => {
                    // console.log(res)
                    if (res.data.code == 200) {
                        console.log(res);
                        for (let i = 0; i < this.shopcarList.length; i++) {
                            for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                                if (this.shopcarList[i].goods[k].ischose) {
                                    this.$set(this.shopcarList[i].goods[k], "isshow", false);
                                }
                            }
                        }

                        for (let i = 0; i < this.shopcarList.length; i++) {
                            let falg = false
                            for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                                if (this.shopcarList[i].goods[k].isshow) {
                                    falg = true;
                                    break
                                }
                            }
                            this.$set(this.shopcarList[i], "isshow", falg);
                        }
                        let newfalg = true
                        for (let i = 0; i < this.shopcarList.length; i++) {
                            if (this.shopcarList[i].isshow) {
                                newfalg = false;
                                break
                            }

                        }
                        this.emptyimgshow = newfalg;
                    }
                })
                .catch((err) => {

                    console.log(err);
                })


        },


        //立即支付
        topay() {
            var isEmpty = true;
            let carid = []
            for (let i = 0; i < this.shopcarList.length; i++) {
                for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                    if (this.shopcarList[i].goods[k].ischose) {
                        carid.push(this.shopcarList[i].goods[k].cart_id + '|' + this.shopcarList[i].goods[k].goods_num)
                        isEmpty = false
                    }
                }
            }

            if (isEmpty) {
                this.$vux.toast.show({ text: '请选择商品', type: 'text' });
            } else {
                this.$router.push({
                    name: 'firmOrder',
                    query: {
                        cardid: carid.join(','),
                        ifcart: 1,
                    }
                });
            }

        },
        //计算购物车总价
        getallprice() {
            let sums = 0;
            for (let i = 0; i < this.shopcarList.length; i++) {
                for (let k = 0; k < this.shopcarList[i].goods.length; k++) {
                    if (this.shopcarList[i].goods[k].ischose && (this.shopcarList[i].goods[k].isshow)) {
                        sums += (this.shopcarList[i].goods[k].goods_price * this.shopcarList[i].goods[k].goods_num)
                    }
                }
            }
            this.sum = sums.toFixed(2)

        }

    },




    //获取cookie
    // getCookie(cname) {
    //   var name = cname + "=";
    //   var ca = document.cookie.split(";");
    //   for (var i = 0; i < ca.length; i++) {
    //     var c = ca[i];
    //     while (c.charAt(0) == " ") c = c.substring(1);
    //     if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
    //   }
    //   return "";
    // }

};


