

export default {
  install(Vue, options) {
    Vue.prototype.hasClass= function(el, className) {
    let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')
    return reg.test(el.className)
  };
  
  Vue.prototype.addClass= function(el, className) {
    if (hasClass(el, className)) {
      return
    }
  
    let newClass = el.className.split(' ')
    newClass.push(className)
    el.className = newClass.join(' ')
  };
  
  Vue.prototype.removeClass= function(el, className) {
    if (!hasClass(el, className)) {
      return
    }
  
    let reg = new RegExp('(^|\\s)' + className + '(\\s|$)', 'g')
    el.className = el.className.replace(reg, ' ')
  };
  
  Vue.prototype.getData= function(el, name, val) {
    let prefix = 'data-'
    if (val) {
      return el.setAttribute(prefix + name, val)
    }
    return el.getAttribute(prefix + name)
  };
  
  Vue.prototype.getRect= function(el) {
    if (el instanceof window.SVGElement) {
      let rect = el.getBoundingClientRect()
      return {
        top: rect.top,
        left: rect.left,
        width: rect.width,
        height: rect.height
      }
    } else {
      return {
        top: el.offsetTop,
        left: el.offsetLeft,
        width: el.offsetWidth,
        height: el.offsetHeight
      }
    }
  };
    //传id返回当前div到屏幕底部的高度,一定要在created之后调用
    Vue.prototype.autoHeight = function(el){
      if (document.getElementById(el)) {
        return document.documentElement.clientHeight - document.querySelector('#' + el).offsetTop;
      } else {
        return 0;
      }
    };
  }
}



