import Vue from 'vue';
import { XHeader, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
    data() {
        return {
            ifTrue: false,
            if_editor: '编辑',
            isEditor: true,
            isStar: true,
            ifshow: false,
            if_shopcar: true,
            ifdelete: false,
            ifHasgo: true,
            showtar: true,
            promptMsg: '没有商品了...',
            showPrice: 'position:absolute;left:0;',
            loveStar: require('../../../assets/love_active.png'),
            selecindex: null,
            ysc: '../../../assets/love_active.png',
            wsc: '../../../assets/love.png',
            Key: this.getCookie("key"),//用户唯一标识
            Favorites_list: [],//关注商品列表
            CurrPage: 1, //当前页码
            PageSize: 20, //每页数据量
            totalCount: 0, //数据总条数
            PageCount: 0, //总页数
            LoadObj: false,//是否已经加载数据
            hasMoreDataObj: false,//是否有下一页数据
            pullDownRefresh: true,//是否启用下拉功能
            pullDownRefreshObj: {
                beforeTxt: "下拉刷新",
                releaseRefreshTxt: "释放立即刷新",
                txt: "加载中...",
                threshold: 40,
                stop: 0//停止时距离父容器顶部的距离
            },
            pullUpLoad: true,//是否启用上拉功能
            pullUpLoadObj: {
                threshold: -40,
                txt: { more: "查看更多", noMore: "没有更多浏览记录~" }
            },
            fullHeight: 0,
            SearchLoad: false,
            DelNum: 0,//删除数量

        }
    },
    components: {
        XHeader,
        Loading,
        Scroll,
    },
    //页面载入
    created: function () {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        this.GetFavoritesList();
    },
    mounted() {

    },
    methods: {
        //初始化数据
        InitParams: function () {
            this.CurrPage = 1; //当前页码
            this.totalCount = 0; //数据总条数
            this.PageCount = 0; //总页数
            this.Favorites_list = [];//订单列表
            this.CheckFa_list = [];//选择商品复选框
            this.hasMoreDataObj = false;//是否有下一页数据
        },
        //下拉执行逻辑
        onPullingDown: function () {
            this.InitParams();
            this.GetFavoritesList();
        },
        //上滑执行逻辑
        onPullingUp: function () {
            // 如果有新数据
            if (this.hasMoreDataObj) {
                this.GetFavoritesList();
            }
        },
        //获取浏览记录
        GetFavoritesList: function () {
            this.SearchLoad = true;
            this.$axios.get('/member_goodsbrowse-browse_list.html', {
                params: {
                    key: this.Key,
                    curpage: this.CurrPage,
                    total_page: this.PageCount,
                    page: this.PageSize,
                }
            }
            ).then((res) => {
                if (res.data.code == 200) {
                    this.Favorites_list.push(...res.data.datas.goodsbrowse_list);

                    this.Favorites_list.forEach(item => {
                        if (item.ifTrue != true) {
                            this.$set(item, "ifTrue", false);
                        }
                    })
                    console.log(this.Favorites_list);

                    // this.$vux.alert.show({ content: '获取成功' });
                    this.totalCount = res.data.datas.count;
                    this.PageCount = res.data.datas.page_total;
                    this.CurrPage++; //当前页码
                    this.hasMoreDataObj = res.data.hasmore;
                    this.LoadObj = true;
                    this.SearchLoad = false;
                }
            })
                .catch((err) => {
                    this.SearchLoad = false;
                    console.log(err);
                })
        },
        editor() {
            this.isEditor = !this.isEditor
            if (this.isEditor) {
                this.if_editor = "编辑";
                this.ifshow = false;
                this.ifHasgo = true;
                this.if_shopcar = true;
                this.ifdelete = false;
                this.showPrice = 'position:absolute;left:0px';
                this.showtar = true;

            } else {
                this.if_editor = "完成";
                this.ifshow = true;
                this.if_shopcar = false;
                this.ifdelete = true;
                this.ifHasgo = false;
                this.showPrice = 'position:absolute;left:30px';
                this.showtar = false;

            }
        },
        //删除关注商品
        DelFavorites: function () {
            console.log(this.DelNum);
            if (this.DelNum > 0) {
                this.SearchLoad = true;
                var goods_ids = '';
                this.Favorites_list.forEach(item => {
                    if (item.ifTrue == true) {
                        goods_ids = goods_ids + item.goods_id + ',';
                        this.DelNum--;
                    }
                })
                console.log(goods_ids);
                this.$axios.post('/member_goodsbrowse-browse_clearall.html', this.$qs.stringify({
                    goods_ids: goods_ids,
                    key: this.Key,
                })
                ).then((res) => {
                    if (res.data.code == 200) {
                        this.SearchLoad = false;
                        goods_ids = '';
                        // this.$vux.alert.show({ content: '删除成功' });
                        this.$vux.toast.show({ text: '删除成功', type: 'text' });
                        this.InitParams();
                        this.GetFavoritesList();
                    }
                })
                    .catch((err) => {
                        this.SearchLoad = false;
                        goods_ids = '';
                        console.log(err);
                    })
            } else {
                this.$vux.toast.show({ text: '请选择商品', type: 'text' });
            }
        },
        //单选
        ClickOne: function (value) {
            if (this.Favorites_list[value].ifTrue == true) {
                this.$set(this.Favorites_list[value], "ifTrue", false);
                this.DelNum--;
            } else if (this.Favorites_list[value].ifTrue == false) {
                this.$set(this.Favorites_list[value], "ifTrue", true);
                this.DelNum++;
            }

            this.ifTrue = false;
            var ifTrueType = 0;
            this.Favorites_list.forEach(item => {
                if (item.ifTrue == true) {
                    ifTrueType++;
                }
            })
            if (ifTrueType == this.Favorites_list.length) {
                this.ifTrue = true;
            }

            // console.log('单选');
            // console.log(this.Favorites_list);
        },
        //全选
        ClickAll: function () {
            this.DelNum = 0;
            if (this.ifTrue == true) {
                this.ifTrue = false;
                this.DelNum = 0;
            } else if (this.ifTrue == false) {
                this.ifTrue = true;
                this.DelNum = this.Favorites_list.length;
            }
            this.Favorites_list.forEach(item => {
                this.$set(item, "ifTrue", this.ifTrue);
                if (this.ifTrue == true) {
                    // this.DelNum++;
                } else if (this.ifTrue == false) {
                    // this.DelNum--;
                }
            })
            console.log(this.DelNum);
            // console.log('全选');
            // console.log(this.Favorites_list);
        },
        //添加关注
        collect() {
            var that = this;
            this.isStar = !this.isStar
            if (this.isStar) {
                that.loveStar = require('../../../assets/love.png')
            } else {
                that.loveStar = require('../../../assets/love_active.png')
            }

        },

        //添加关注商品
        Addfavorites: function (index) {
            this.SearchLoad = true;
            this.$axios.post('/member_favorites-favorites_add.html', this.$qs.stringify({
                goods_id: this.Favorites_list[index].goods_id,
                key: this.Key,
            })
            ).then((res) => {
                if (res.data.code == 200) {
                    this.SearchLoad = false;
                    // this.Is_favorate = true;
                    this.$set(this.Favorites_list[index], "is_favorate", true);
                    this.$vux.toast.show({ text: '关注成功', type: 'text' });
                }
            })
                .catch((err) => {
                    this.SearchLoad = false;
                    this.$vux.toast.show({ text: '关注失败', type: 'text' });
                    console.log(err);
                })
        },
        //删除关注商品
        Delfavorites: function (index) {
            console.log(index);
            this.SearchLoad = true;
            this.$axios.post('/member_favorites-favorites_del.html', this.$qs.stringify({
                fav_id: this.Favorites_list[index].goods_id,
                key: this.Key,
            })
            ).then((res) => {
                if (res.data.code == 200) {
                    this.SearchLoad = false;
                    // this.Is_favorate = false;
                    this.$set(this.Favorites_list[index], "is_favorate", false);
                    this.$vux.toast.show({ text: '取消关注', type: 'text' });
                }
            })
                .catch((err) => {
                    this.SearchLoad = false;
                    this.$vux.toast.show({ text: '取消关注失败', type: 'text' });
                    console.log(err);
                })
        },
        //跳转商品详情
        GoGoodsDetail: function (goodsid) {
            if (this.if_shopcar) {
                this.$router.push({
                    name: 'gooddetail',
                    query: {
                        goodsid: goodsid,
                    }
                });
            }
        },
    },
}














