export default {
    name: "App",
    data() {
        return {
            transitionName: ""
        };
    },
    watch: {
        $route(to, from) {
            //    console.log('现在路由:',to.path.split('/')[1],'来自路由:',from.path.split('/')[1],'现在的动画:',this.transitionName)

            // const toDepth = to.path.split("/")[1].length;
            // const fromDepth = from.path.split("/")[1].length;
            this.transitionName = "slide-right";
        }
    }
};



