import Vue from 'vue';
import { XInput, Group, XButton, Cell, Checker, XHeader } from 'vux';
export default {
  components: {
    XInput,
    XButton,
    Group,
    Cell,
    Checker,
    XHeader
  },
  name: 'login',
  data() {
    return {
      pho: '',//手机号码
      img_code: '',//图形验证码
      pho_code: '',//手机验证码
      code_text: "获取验证码",
      img_code_url_k:'',//获取图形验证码图片地址url的key
      img_code_url:'',//图形验证码图片地址url
      err: '',
      time: 0,
      but_style: {
        background: '#f16565',
        color: '#fff',
      }
    }
  },

  //数据初始化
  mounted:function($routey){
    this.$axios.get('/seccode-makecodekey.html', {}).then((res) => {
			if (res.data.code=="200") {
       this.img_code_url_k=res.data.datas.codekey;
       this.img_code_url=this.$globalScope.root+'/seccode-makecode.html?k='+res.data.datas.codekey+'&t=' + Math.random();
			}
		}).catch((err) => {
			console.log(err);
		})
  },
  methods: {
    //注册
    register() {
      this.$router.push({ name: 'register' });
    },
    //图形验证码
    img_code_cli() {
      //this.img_code_url=this.ApiUrl+'/seccode-makecode.html?k='+this.img_code_url_k+'&t=' + Math.random();
    },
    //手机验证码
    code_text_cli() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        if (this.time == 0) {
          this.$axios.get('/connect-get_sms_captcha.html', {
            params: { "type": 2, key: '', "sec_val": this.img_code, "sec_key": this.img_code_url_k, "phone": this.pho }
          }).then((res) => {
            if (res.data.state) {
              this.but_style.background = "#ccc";
              //倒计时显示
              clearInterval(this.interval);
              this.time = res.data.sms_time;
              this.code_text = this.time + "秒后重试";
              this.interval = setInterval(() => {
                if (this.time > 0) {
                  this.time--
                  this.code_text = this.time + "秒后重试";
                } else {
                  this.code_text = "获取验证码";
                  this.but_style.background = "#f16565";
                  clearInterval(this.interval);
                }
              }, 1000)
            }
            else {
              this.$vux.alert.show({
                title: '失败',
                content: res.data.msg
              })
            }
          }).catch((err) => {
            console.log(err);
          })
        }
      } else {
        this.$vux.alert.show({
          title: '失败',
          content: '手机号码格式错误'
        })
      }
    },

    //登录
    login_subm() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        if (this.pho_code != '' && this.pho_code) {
          this.$axios.post('/loginm-index.html', this.$qs.stringify({
            auth_code: this.pho_code,
            client: "wap",
            username: this.pho
          })
          ).then((res) => {
            if (res.data.code == 200) {
              this.setCookie("key", res.data.datas.key);
              this.setCookie("username", res.data.datas.username);
              this.setCookie("userid", res.data.datas.userid);
              this.setCookie("storeid", res.data.datas.storeid);
              this.$router.go(-1);
            } else {
              this.$vux.alert.show({
                title: '失败',
                content: res.data.datas.error
              })
            }
          })
            .catch((err) => {
              console.log(err);
            })
        } else {
          this.$vux.alert.show({
            title: '失败',
            content: '手机验证码格式不正确'
          })
        }
      } else {
        this.$vux.alert.show({
          title: '失败',
          content: '手机号码格式错误'
        })
      }
    },
    //微信登录
    wx_login() {
        this.$vux.toast.show({ text: '暂无微信登录', type: 'text' });
      // location.href = this.ApiUrl + '/connect-index.html';
    }
  }
}





