import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Alert,
Scroll
},
name: 'shoplist',

data() {
return {
store_list: [], //店铺列表
CurrPage: 1, //当前页码
PageSize: 20, //每页数据量
totalCount: 0, //数据总条数
PageCount: 0, //总页数
LoadObj: false, //是否已经加载数据
hasMoreDataObj: false, //是否有下一页数据
pullDownRefresh: false, //是否启用下拉功能
pullDownRefreshObj: {
beforeTxt: "下拉刷新",
releaseRefreshTxt: "释放立即刷新",
txt: "加载中...",
threshold: 40,
stop: 0 
},
pullUpLoad: true, //是否启用上拉功能
pullUpLoadObj: {
threshold: -40,
txt: { more: "查看更多", noMore: "我是有底线的" }
},
Title: '', //分类名称(获取全部商品时不传)
Gc_id: '', //分类ID(获取全部商品时不传)
Order: 1, //排序规则 1：正序
Key: 2, //查询规则 1:销量 2:综合 3:价格
fullHeight: 0,
SearchLoad: false,
Keyword: '', //分类名称(获取全部商品时不传)
Type: '', //商品类型
emptyshow: false,
}
},

created: function() {



if (this.$route.query.keyword != undefined || this.$route.query.type != undefined) {
this.Title = this.$route.query.keyword == undefined ? '' : this.$route.query.keyword;
this.Type = this.$route.query.type;
this.Getshoplist();
} else if (this.$route.query.title != undefined || this.$route.query.gc_id != undefined) {
this.Title = this.$route.query.title == undefined ? '' : this.$route.query.title;
this.Gc_id = this.$route.query.gc_id;
this.Getshoplist();
} else {
this.Getshoplist();
}


},

mounted: function() {
this.fullHeight = this.autoHeight('conent');
console.log(this.fullHeight);
},

methods: {

InitParams: function() {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.store_list = []; 
this.hasMoreDataObj = false; 
},

onPullingDown: function() {
this.InitParams();
this.Getshoplist();
},

onPullingUp: function() {

if (this.hasMoreDataObj) {
this.Getshoplist();
}
},

ChangeType: function(type) {
this.Key = type;
if (type == 3) {
if (this.Order == 1) {
this.Order = 2;
} else if (this.Order == 2) {
this.Order = 1;
}
}
this.InitParams();
this.Getshoplist();
},

Getshoplist: function() {
this.emptyshow = false;
this.SearchLoad = true;
this.$axios.get('/store-store_search.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
title: this.Title,
gc_id: this.Gc_id,
order: this.Order,
key: this.Key,
}
}).then((res) => {
console.log(res)
if (res.data.code == 200) {
this.store_list.push(...res.data.datas.store_list);

this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = true;
this.SearchLoad = false;
if (this.store_list.length == 0) {
this.emptyshow = true;
}
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},


},
}


