import Vue from 'vue';
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XInput,
XButton,
Group,
XHeader
},
data() {
return {
name: '',
Key: this.getCookie("key"),
}
},
name: 'reviseNickname',

created: function () {
if (this.Key == '') {
this.$router.push('/login');
}
},
methods: {
reviseNickname_subm() {
if (this.name && this.name != '') {
this.$axios.post('/member_change-savename.html', this.$qs.stringify({
key: this.Key,
new_name: this.name
})
).then((res) => {

if (res.data.code == 200) {
this.setCookie("username", this.name)
this.$router.go(-1);
} else {
this.$vux.alert.show({
title: '失败',
content: result.body.datas.error
})
}
})
.catch((err) => {
console.log(err);
})

} else {
this.$vux.alert.show({
title: '失败',
content: '昵称格式错误'
})
}
}
}
}


