import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Cell, XNumber, Group, Loading, Selector, CellBox } from 'vux'
const list = () => ['全部', '待付款', '待发货', '待收货', '待评价']

export default {
  data() {
    return {
      list2: list(),
      demo2: '全部',
      index: 0,
      value: 0,
      value2: 0,
      state_now: 0,
      eventId: 0,
      codingState: '',
      logistics: "",
      Key: this.getCookie("key"),
      //Key: '6c921998c5128a47e9ac436dc7f95b4f',
      roundValue: 0,
      goods_img_360: '',//商品图片
      goods_name: '',//商品名字
      goods_price: 0,//商品价格
      iftrue: false,//是否可以退货
      buyer_message: '',//退款说明
      order_id: this.$route.query.order_id,
      order_goods_id: this.$route.query.goods_id,
      order_state: this.$route.query.order_state,
      refund_pic: [],
      SearchLoad: false,
      order_amount: 3.6,
      value: "",
      goods_num: 0,//最大商品数量
      orderIndex: null,
      status: 'ready',
      files: [],
      point: {},
      val: 0,//数量值
      uploading: false,
      percent: 0,
      reasonitems: [],
      reason_id: 0,//原因选中id
      data: '',//退款原因点击的数据
      showContent001: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XNumber,
    Group,
    Cell,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Selector,
    CellBox
  },
  //页面载入
  created: function () {
    this.roundValue = this.goods_num;
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted() {
    this.state_now = this.$route.params.state;
    this.roundValue = this.goods_num;
    console.log(this.order_state)
    if (this.order_state == '待发货') {
      this.GetaftergoodMsg();
      this.iftrue = true;
    } else {
      this.GetaftergoodMsg2();
      this.iftrue = false;
    }
  },
  methods: {
    //退款商品信息(待发货)
    GetaftergoodMsg: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_refund-refund_all_form.html', {
        params: {
          order_id: this.order_id,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          let orderMsg = res.data.datas.order;
          let aftergoods = res.data.datas.goods_list[0];
          this.goods_img_360 = aftergoods.goods_img_360;
          this.goods_name = aftergoods.goods_name;
          this.goods_price = aftergoods.goods_price;
          this.goods_num = parseInt(aftergoods.goods_num);
          this.order_amount = aftergoods.goods_price * this.val;
          console.log(this.order_amount)
          this.hasMoreDataObj = res.data.hasmore;
          // this.LoadObj = true;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    change(val) {
      //console.log('change', val)
      this.val = val;
      console.log(this.val)
    },
    //退货/退款商品信息(已发货)
    GetaftergoodMsg2: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_refund-refund_form.html', {
        params: {
          order_id: this.order_id,
          order_goods_id: this.order_goods_id,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          let orderMsg = res.data.datas.order;
          let aftergoods = res.data.datas.goods;
          this.goods_img_360 = aftergoods.goods_img_360;
          this.goods_name = aftergoods.goods_name;
          this.goods_price = aftergoods.goods_price;
          this.hasMoreDataObj = res.data.hasmore;
          this.goods_num = parseInt(aftergoods.goods_num);
          this.order_amount = aftergoods.goods_price * this.roundValue;
          // this.LoadObj = true;
          this.reasonitems = res.data.datas.reason_list;
          console.log(this.order_amount)
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },

    //点击提交申请按钮
    submitApplication() {
      var refund = new Array();
        refund = new Array();

        for (var pic in this.refund_pic) {
          refund[pic] = new Array();
          refund[pic] = this.refund_pic[pic].file_name;
        }
      if (this.order_state == '待发货') {
        this.$axios.post('/member_refund-refund_all_post.html', this.$qs.stringify({
          order_id: this.order_id,
          key: this.Key,
          buyer_message: this.buyer_message,
          refund_pic: this.refund,
        })
        ).then((res) => {
          if (res.data.code == 200) {
            this.SearchLoad = false;
            this.DelType = 1;
            this.DelNum--;
          }
        })
          .catch((err) => {
            this.SearchLoad = false;
            console.log(err);
          })
      } else {

        var refund = new Array();
        refund = new Array();

        for (var pic in this.refund_pic) {
          refund[pic] = new Array();
          refund[pic] = this.refund_pic[pic].file_name;
        }
        console.log(refund);
        this.refund_amount = this.goods_price * this.roundValue;
        this.$axios.post('/member_refund-refund_post.html', this.$qs.stringify({
          order_id: this.order_id,
          order_goods_id: this.order_goods_id,
          key: this.Key,
          buyer_message: this.buyer_message,
          refund_type: this.value,
          reason_id: this.reason_id,
          refund_amount: this.refund_amount,
          refund_pic: refund,
        })
        ).then((res) => {
          if (res.data.code == 200) {
            this.SearchLoad = false;
            this.DelType = 1;
            this.DelNum--;
          }
        })
          .catch((err) => {
            this.SearchLoad = false;
            console.log(err);
          })
      }
    },

    choose(val) {
      this.value = val;
    },
    choose2(val) {
      this.value2 = val;
    },

    add() {
      if (this.files.length >= 3) {
        this.$vux.toast.show({ text: '最多上传3张图片', type: 'text' });
        return
      }
      this.$refs.file.click();
      // console.log(this.files);
    },

    finished() {
      this.files = []
      this.status = 'ready'
    },
    remove(index) {
      // this.files.splice(index, 1)
      this.refund_pic.splice(index, 1);
    },
    fileChanged() {
      this.SearchLoad = true;
      const list = this.$refs.file.files
      for (let i = 0; i < list.length; i++) {
        if (!this.isContain(list[i])) {
          const item = {
            name: list[i].name,
            size: list[i].size,
            file: list[i]
          }
          // console.log(item.size);
          if (item.size >= 6000000) {
            this.$vux.toast.show({ text: '每张图片不能超过6M', type: 'text' });
            // return;
          } else {
            this.html5Reader(list[i], item)
            this.files.push(item)
          }
        }
      }
      console.log(this.files);
      if (this.files.length > 0) {
        const formData = new FormData()
        this.files.forEach((item) => {
          formData.append('refund_pic', item.file)
          this.files.splice(item, 1);
        })
        console.log(this.files);
        formData.append('key', this.Key);
        //额外设置请求时间
        this.$axios.defaults.timeout = 60000;
        this.$axios.post('/member_refund-upload_pic.html', formData)
          .then((res) => {
            // console.log(res);
            if (res.data.code == 200) {
              this.refund_pic.push({ file_name: res.data.datas.file_name, pic: res.data.datas.pic });
              this.SearchLoad = false;
              console.log(this.refund_pic);
              // this.$vux.toast.show({ text: '上传成功', type: 'text' });
            }
          })
          .catch((err) => {
            console.log(err);
            this.SearchLoad = false;
            this.$vux.toast.show({ text: '网络不给力', type: 'text' });
          })
      }
      this.$refs.file.value = ''
    },
    // 将图片文件转成BASE64格式
    html5Reader(file, item) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.$set(item, 'src', e.target.result)
      }
      reader.readAsDataURL(file)
    },
    onChange(val) {
      console.log(val)
    },
    isContain(file) {
      return this.files.find((item) => item.name === file.name && item.size === file.size)
    },
  },
}














