import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      state: 1,
      order_state: 5,
      orderNum: '5645216463554655685456',
      shopState: '待付款',
      codingState: '进行中',
      codingTime: '2017-10-11',
      codingMoney: '632.0',
      Key: this.getCookie("key"),//用户唯一标识
      State_type: '',//state_new待付款,state_send已付款,state_confirm已收货,state_cancel已取消
      Order_key: '',
      LoadObj: false,//是否已经加载数据
      Order_list: [],//淘宝订单列表
      jdOrder_list: [],//京东订单列表
      CurrPage: 1, //当前页码
      order_status: '',//订单状态
      jd_order_status: '',
      orderState: '',//订单状态显示
      jdorderState: '',//订单状态显示
      PageSize: 20, //每页数据量
      totalCount: 0, //数据总条数
      PageCount: 0, //总页数
      hasMoreDataObj: false,//是否有下一页数据
      pullDownRefresh: true,//是否启用下拉功能
      pullDownRefreshObj: {
        beforeTxt: "下拉刷新",
        releaseRefreshTxt: "释放立即刷新",
        txt: "加载中...",
        threshold: 40,
        stop: 0//停止时距离父容器顶部的距离
      },
      pullUpLoad: true,//是否启用上拉功能
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "下拉刷新查看更多订单...", noMore: "没有更多订单了..." }
      },
      fullHeight: 0,
      SearchLoad: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetOrderList();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
  },
  filters: {
    //时间转换
    ChangeTime(value) {
      if (value.length == 10) {
        value = value * 1000;
      }
      // console.log(value);
      var date = new Date(value);
      // console.log(date);
      var Y = date.getFullYear() + '-';
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      var D = date.getDate() + ' ';
      var h = date.getHours() + ':';
      var m = date.getMinutes() + ':';
      var s = date.getSeconds();
      var newdate = Y + M + D + h + m + s;
      return newdate;
    },

    //淘宝状态显示
    tbstate(value) {
      console.log(value)
      if (value == 1) {
        return '进行中'
      } else if (value == 2) {
        return '进行中'
      } else if (value == 3) {
        return '已存入'
      } else if (value == 4) {
        return '无效'
      }
    },
    tbstate2(value) {
      if (value == 1) {
        return '待付款'
      } else if (value == 2) {
        return '已付款'
      } else if (value == 3) {
        return '交易完成'
      } else if (value == 4) {
        return '交易关闭'
      } else {
        return '待补单'
      }
    },

    //京东状态显示
    jdstate(value) {
      console.log(value)
      if (value == 1) {
        return '进行中'
      } else if (value == 2) {
        return '进行中'
      } else if (value == 0) {
        return '已存入'
      } else if (value == 4) {
        return '无效'
      }
    },
    jdstate2(value) {
      if (value == 1) {
        return '待付款'
      } else if (value == 2) {
        return '已付款'
      } else if (value == 0) {
        return '交易完成'
      } else if (value == 4) {
        return '交易关闭'
      } else {
        return '待补单'
      }
    },
  },
  methods: {

    //初始化数据
    InitParams: function () {
      this.CurrPage = 1; //当前页码
      this.totalCount = 0; //数据总条数
      this.PageCount = 0; //总页数
      this.Order_list = [];//订单列表
      this.hasMoreDataObj = false;//是否有下一页数据
    },
    //下拉执行逻辑
    onPullingDown: function () {
      this.InitParams();
      this.GetOrderList();
    },
    //上滑执行逻辑
    onPullingUp: function () {
      // 如果有新数据
      if (this.hasMoreDataObj) {
        this.GetOrderList();
      }
    },

    //切换标签
    ChangeType: function (type) {
      if (type == 5 || type == 10) {
        this.order_state = type;
      }
      console.log(this.order_state)
      if (this.order_state == 5) {
        if (type == 1) {
          this.State_type = 'state_send';
          this.state_now = 1;
        } else if (type == 2) {
          this.State_type = 'state_completed';
          this.state_now = 2;
        } else if (type == 3) {
          this.State_type = 'state_close';
          this.state_now = 3;
        } else {
          this.State_type = '';
          this.state_now = 0;
        }
      } else {
        if (type == 1) {
          this.State_type = 'state_send';
          this.state_now = 1;
        } else if (type == 2) {
          this.State_type = 'state_confirm';
          this.state_now = 2;
        } else if (type == 3) {
          this.State_type = 'state_cancel';
          this.state_now = 3;
        } else {
          this.State_type = '';
          this.state_now = 0;
        }
      }

      this.InitParams();
      this.GetOrderList();
    },
    //获取订单
    GetOrderList() {
      if (this.order_state == 5) {
        this.GetTBOrderList();
      } else if (this.order_state == 10) {
        this.GetJDOrderList();
      }
    },
    //获取京东订单
    GetJDOrderList: function () {
      this.SearchLoad = true;
      this.$axios.post('/member_order-jd_order_list.html?page=' + this.PageSize + '&curpage=' + this.CurrPage + '', this.$qs.stringify({
        state_type: this.State_type,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.jdOrder_list = [];
          this.jdOrder_list.push(...res.data.datas.order_group_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //当前页码
          this.hasMoreDataObj = res.data.hasmore;
          this.LoadObj = true;
          this.SearchLoad = false;
          // this.jdOrder_list.forEach(item => {
          //   console.log(item.order_status);
          //   if (item.order_status == 1) {
          //     this.order_status = '待付款'
          //     this.jdorderState = '进行中'
          //   } else if (item.order_status == 2) {
          //     this.order_status = '已付款'
          //     this.jdorderState = '进行中'
          //   } else if (item.order_status == 0) {
          //     this.order_status = '交易关闭'
          //     this.jdorderState = '无效'
          //   } else if (item.order_status == 4) {
          //     this.order_status = '交易完成'
          //     this.jdorderState = '已存入'
          //   } else {
          //     this.order_status = '待补单'
          //   }
          // })
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //获取淘宝订单
    GetTBOrderList: function () {
      this.SearchLoad = true;
      this.$axios.post('/member_order-taobao_order_list.html?page=' + this.PageSize + '&curpage=' + this.CurrPage + '', this.$qs.stringify({
        state_type: this.State_type,
        order_key: '',
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.Order_list = [];
          this.Order_list.push(...res.data.datas.order_group_list);
          console.log(this.Order_list);
          // this.Order_list.forEach(item => {
          //   console.log("222" + item.order_status);
          //   console.log(this)
          //   if (item.order_status == 1) {
          //     this.order_status = '待付款'
          //     this.orderState = '进行中'
          //   } else if (item.order_status == 2) {
          //     this.order_status = '已付款'
          //     this.orderState = '进行中'
          //   } else if (item.order_status == 3) {
          //     this.order_status = '交易完成'
          //     this.orderState = '已存入'
          //   } else if (item.order_status == 4) {
          //     this.order_status = '交易关闭'
          //     this.orderState = '无效'
          //   } else {
          //     this.order_status = '待补单'
          //   }
          // })

          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //当前页码
          this.hasMoreDataObj = res.data.hasmore;
          this.LoadObj = true;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //跳转至淘宝补单
    jumpTosinglesupplement() {
      this.$router.push({ name: 'singleSupplement' });
    },
  },
}














