import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      state: 1,
      orderNum: '5645216463554655685456',
      shopState: '待付款',
      codingState: '2',
      seller_state: '',
      showNum: false,
      add_time: '2017-10-11 15:50',
      refund_amount: '632.0',
      Key: this.getCookie("key"),//用户唯一标识
      JDState_type: '',//state_new待付款,state_send已付款,state_confirm已收货,state_cancel已取消
      TBState_type: '',//state_new待付款,state_send已付款,state_completed已收货,state_close已取消,state_choose待补单
      Order_key: '',
      refund_list: [],//退款订单列表
      retrun_list: [],//退货订单列表
      CurrPage: 1, //当前页码
      PageSize: 20, //每页数据量
      totalCount: 0, //数据总条数
      PageCount: 0, //总页数
      hasMoreDataObj: false,//是否有下一页数据
      pullDownRefresh: true,//是否启用下拉功能
      pullDownRefreshObj: {
        beforeTxt: "下拉刷新",
        releaseRefreshTxt: "释放立即刷新",
        txt: "加载中...",
        threshold: 40,
        stop: 0//停止时距离父容器顶部的距离
      },
      pullUpLoad: true,//是否启用上拉功能
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "下拉刷新查看更多订单...", noMore: "没有更多订单了..." }
      },
      fullHeight: 0,
      SearchLoad: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  computed: {
  },
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetOrderList();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
  },
  methods: {
    jumpToreturndetail(value, state) {
      this.$router.push({ name: 'returnmoneyDetail', query: { refund_id: value, state: state } });
    },
    //切换退款退货
    ChangeType: function (type) {
      this.state = type;
      console.log(this.state)
      this.GetOrderList();
    },
    //初始化数据
    InitParams: function () {
      this.CurrPage = 1; //当前页码
      this.totalCount = 0; //数据总条数
      this.PageCount = 0; //总页数
      this.Order_list = [];//订单列表
      this.hasMoreDataObj = false;//是否有下一页数据
    },
    //下拉执行逻辑
    onPullingDown: function () {
      this.InitParams();
      this.GetOrderList();
    },
    //上滑执行逻辑
    onPullingUp: function () {
      // 如果有新数据
      if (this.hasMoreDataObj) {
        this.GetOrderList();
      }
    },
    //获取订单
    GetOrderList() {
      if (this.state == 1) {
        this.GetrefundOrderList();
      } else if (this.state == 2) {
        this.GetRetrungoodsOrderList();
      }
    },
    //获取退款订单
    GetrefundOrderList: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_refund-get_refund_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          this.refund_list = [];
          this.refund_list.push(...res.data.datas.refund_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //当前页码
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //获取退货订单
    GetRetrungoodsOrderList: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_return-get_return_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          this.retrun_list = [];
          this.retrun_list.push(...res.data.datas.return_list);
          console.log(this.retrun_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //当前页码
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
  },
}














