import Vue from 'vue';
import { XHeader } from 'vux';
export default {
	components: {
		XHeader
	},
	data() {
		return {
			selectAddressId: '',//设置默认地址ID
			datas: '',//地址列表
			pagetype: this.$route.query.type == undefined ? '' : this.$route.query.type,//获取来源
			Key: this.getCookie("key"),
		}
	},
	name: 'addressAdministration',
	//页面载入
	created: function () {
		if (this.Key == '') {
			this.$router.push('/login');
		}
	},
	//初始化数据
	mounted: function () {
		this.address_list();
	},
	methods: {
		//跳转地址详情
		jumpToreviseaddress(value) {
			this.$router.push({ name: 'addAddress', params: { id: value } });
		},
		//设置默认地址
		setDefaultAddress(addressItem) {
			console.log("设置默认地址");
			var is_default = addressItem.is_default == '0' ? '1' : '0';
			console.log(addressItem);
			console.log(is_default);
			this.$axios.post('/member_address-address_edit.html', this.$qs.stringify({
				key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
				true_name: addressItem.true_name,
				mob_phone: addressItem.mob_phone,
				city_id: addressItem.city_id,
				area_id: addressItem.area_id,
				address: addressItem.address,
				area_info: addressItem.area_info,
				address_id: addressItem.address_id,
				// is_default: "1"
				is_default: is_default
			})
			).then((res) => {
				if (res.data.code == 200) {
					if (is_default == 1) {
						this.selectAddressId = addressItem.address_id;
					}
					else {
						this.selectAddressId = '';
					}

					// this.$set(addressItem, 'address_id', is_default);
					// console.log(addressItem);
					if (this.pagetype == 'firmOrder') {
						this.$router.go(-1)
					}
					this.address_list();
				} else {
					this.$vux.alert.show({
						title: '失败',
						content: res.data.datas.error
					})
				}
			})
				.catch((err) => {
					console.log(err);
				})

		},
		//删除地址
		dele(id) {
			var _this = this;
			this.$vux.confirm.show({
				title: '提示', content: '删除地址',
				onCancel() { },
				onConfirm() {
					_this.$axios.post('/member_address-address_del.html', _this.$qs.stringify({
						key: _this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
						address_id: id
					})
					).then((res) => {
						if (res.data.code == 200) {
							if (res.data.datas != 0) {
								_this.address_list();
							} else {
								_this.$router.push({ path: '/login' });
							}
						} else {
							_this.$vux.alert.show({
								title: '失败',
								content: res.data.datas.error
							})
						}
					})
						.catch((err) => {
							console.log(err);
						})
				}
			})
		},
		//获取地址列表
		address_list() {
			this.$axios.post('/member_address-address_list.html', this.$qs.stringify({
				key: this.Key//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
			})
			).then((res) => {
				if (res.data.code == 200) {
					if (res.data.login != 0) {
						this.datas = res.data.datas.address_list;
						for (var i = 0; i < this.datas.length; i++) {
							if (this.datas[i].is_default == "1") {
								this.selectAddressId = this.datas[i].address_id;
							}
						}
					} else {
						this.$router.push({ path: '/login' });
					}
				} else {
					this.$vux.alert.show({
						title: '失败',
						content: res.data.datas.error
					})
				}
			})
				.catch((err) => {
					console.log(err);
				})
		}
	},

}



