
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Countup from 'vux/src/components/countup/index.vue'


export default {
components: {
Loading,
Popup,
Countup,
},
name: 'firmOrder',

data() {
return {
Store_Cart_list: [], //订单信息
Cart_id: '',//商品id|数量,商品id|数量,
Address_api: [],//地址API
Address_info: [],//地址信息
AllContent_list: [],//所有店铺运费信息
ExContent_list: [],//个别店铺运费信息
ExStoreid: 0,//选择运费时个别店铺id
AllExpress: 0,//总运费
Goods_list: [], //商品信息信息
Poverty_relief: 0,//扶贫积分
Fcdata: [],//获赠信息
Order_amount: 0,//订单总金额
Vat_hash: '',//增值税发票哈希值
Offpay_hash: '',//php验证使用
Offpay_hash_batch: '',//php验证使用
Invoice_id: '',//发票Id
SearchLoad: false,
ScoreShow: false,//是否显示输入可用积分
TicketShow: false,//是否显示输入可用电子券
PayShow: false,//是否显示支付类型
PayClose: true,//是否显示放弃支付
PayType: 0,//支付类型
PayText: '中国工商银行',
Paylist: [],//支付方式
PayName: 'online',//在线支付
VoucherShow: false,//是否显示开具发票
VoucherType: 0,//是否开具发票
VoucherText: '不需要发票',
VoucherSelect: 'person',//发票类型company公司/person个人
VoucherSelectText: '',//发票抬头
Score: '',//积分消费
Ticket: '',//电子券抵用
Fcode: 0,//是否为F码商品
Paymessage: '',//买家留言
PaymessageAll: '',//所有买家留言
Fredkg: '',//各个店铺运费（店铺id=5,店铺id=10）
WlShow: false,//是否显示选择物流
WlType: 0,//选择物流类型
InputShow: false,//是否显示单位名称
Voucher: '',//优惠券信息
Store_voucher_info: [],//使用的优惠券
Key: this.getCookie("key"),//用户唯一标识6ca9f3434c210705c756126cd5bb6d78
Cardid: this.$route.query.cardid,//购物车ID
Goodsid: this.$route.query.goodsid,//商品ID
PayNum: this.$route.query.paynum,//商品数量
IfCart: this.$route.query.ifcart,//是否购物车（0否1是）
Cart: '',//购物车信息
SumbutType: true,//提交状态
Pay_sn: '',//支付单号
}
},

created: function () {


if (this.Key == '') {
this.$router.push('/login');
}
if (this.IfCart == 0) {
this.Cart = this.Goodsid + '|' + this.PayNum;
}
else if (this.IfCart == 1) {
this.Cart = this.Cardid;
}
this.GetStoreCartList();
},

mounted: function () {

},
filters: {
Twonumber: function (value) {
value = Number(value);
return value.toFixed(2);
}
},
watch: {
Score: function (a, b) {


if (Number(a) < 0) {
this.Score = b;
}



console.log(a.toString());
if (a.toString().indexOf(".") != -1 && a.toString().split(".")[1].length > 2) {
this.Score = b;
}
if (Number(a) > Number(this.Fcdata.maxscore)) {
this.Score = b;
}
if (Number(a) + Number(this.Ticket) > Number(this.Order_amount)) {
this.Score = b;
}
},
Ticket: function (a, b) {


if (Number(a) < 0) {
this.Ticket = b;
}



if (a.toString().indexOf(".") != -1 && a.toString().split(".")[1].length > 2) {
this.Ticket = b;
}
if (Number(a) > Number(this.Fcdata.maxticket)) {
this.Ticket = b;
}
if (Number(a) + Number(this.Score) > Number(this.Order_amount)) {
this.Ticket = b;
}
},
},
computed: {

Com_Order_amount: function () {

return Number(this.Order_amount) - Number(this.Score) - Number(this.Ticket);
}
},

methods: {

LitCom_Order_amount: function (total, freight, price) {
if (total == undefined) { total = 0 }
if (freight == undefined) { freight = 0 }
if (price == undefined) { price = 0 }




return Number(total) + Number(freight) - Number(price);
},

GetStoreCartList: function () {
this.SearchLoad = true;


var coupon = sessionStorage.getItem("coupon");
this.Store_voucher_info = [];
if (coupon != '') {
var couponlist = JSON.parse(coupon);
console.log(couponlist);
this.Store_voucher_info = new Array();
for (var i in couponlist) {
this.Store_voucher_info[couponlist[i].split(',')[0]] = new Array();
this.Store_voucher_info[couponlist[i].split(',')[0]] = couponlist[i].split(',')[1].split('|')[0];
}






console.log(this.Store_voucher_info);
}

this.$axios.post('/member_buy-buy_step1.html', this.$qs.stringify({
ifcart: this.IfCart,
cart_id: this.Cart,
key: this.Key,
voucher: this.Store_voucher_info,
})
).then((res) => {
if (res.data.code == 200) {
this.Address_api = res.data.datas.address_api;
if (this.Address_api != '') {
this.Offpay_hash = this.Address_api.offpay_hash;
this.Offpay_hash_batch = this.Address_api.offpay_hash_batch;
this.AllContent_list = this.Address_api.content;
}
this.Address_info = res.data.datas.address_info;
this.Goods_list = res.data.datas.store_cart_list;
var stornum = 0;
var freightnum = 0;


for (var i in this.Goods_list) {
stornum = 0;
freightnum = 0;
this.Goods_list[i].goods_list.forEach(goods => {
stornum = stornum + goods.goods_num * 1;
freightnum = freightnum + goods.goods_freight * 1;

if (this.IfCart == 0) {
this.Cart_id = this.Cart_id + goods.goods_id + '|' + goods.goods_num + ',';
} else {
this.Cart_id = this.Cart_id + goods.cart_id + '|' + goods.goods_num + ',';
}
})
this.$set(this.Goods_list[i], 'store_goods_num', stornum);
}


for (var ex in this.AllContent_list) {
for (var good in this.Goods_list) {
if (ex == good) {
this.$set(this.Goods_list[good], 'store_goods_freight', this.AllContent_list[ex].express);
this.WlType = this.AllContent_list[ex].express;
}
}
}




















this.Voucher = '';
for (var good in this.Goods_list) {
this.Goods_list[good].store_voucher_info
this.Voucher = this.Voucher + this.Goods_list[good].store_voucher_info.voucher_t_id + '|' + this.Goods_list[good].store_voucher_info.voucher_store_id + '|' + this.Goods_list[good].store_voucher_info.voucher_price + ',';
}

console.log(this.Goods_list);

this.Poverty_relief = res.data.datas.poverty_relief;
this.Fcdata = res.data.datas.fcdata;
this.Order_amount = this.ComputedEx();
this.Vat_hash = res.data.datas.vat_hash;
this.Invoice_id = res.data.datas.inv_info.inv_id;
this.Fcode = 0;
this.ComAllExpress();
this.SearchLoad = false;
} else {
this.SumbutType = false;
this.SearchLoad = false;
if (res.data.datas.error == '地区请求失败') {
this.$vux.toast.show({ text: '请设置收货地址', type: 'text', width: '40%' });
}
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

ComputedEx: function () {
this.Order_amount = 0;
for (var good in this.Goods_list) {
this.Order_amount = Number(this.Order_amount) + Number(this.Goods_list[good].store_goods_freight) + Number(this.Goods_list[good].store_goods_total);
}
return this.Order_amount;
},

GetCalc: function (goodsid, areaid) {
this.$axios.get('/goods-calc.html', {
params: {
goods_id: goodsid,
area_id: areaid,
}
}).then((res) => {
if (res.data.code == 200) {
console.log(res.data.datas);
}
})
.catch((err) => {
console.log(err);
})
},

ChangePayType: function (paytype) {
this.PayType = paytype;



},

SetPayType: function () {
this.PayClose = false;
this.PayShow = false;
console.log(this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn=' + this.Pay_sn + '&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '')
window.location.replace(this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn=' + this.Pay_sn + '&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '');
},

ChangeVoucherType: function (index) {
if (index == 0) {
this.VoucherType = index;
this.VoucherText = '不需要发票';
} else if (index == 1) {
this.VoucherType = index;
this.VoucherText = '需要发票';
}

},

GetVoucherinfo: function () {
this.SearchLoad = true;
this.$axios.post('/member_invoice-invoice_add.html', this.$qs.stringify({
key: this.Key,
inv_title_select: this.VoucherSelect,
inv_title: this.VoucherSelectText,
inv_content: '',
})).then((res) => {
if (res.data.code == 200) {
console.log(res.data.datas);
this.Invoice_id = res.data.datas.inv_id;
console.log(this.Invoice_id);
this.VoucherShow = false;
this.SearchLoad = false;
}
})
.catch((err) => {
console.log(err);
})
},

CloseScore: function () {
if (this.ScoreShow) {
this.Score = '';
}
},

CloseTicket: function () {
if (this.TicketShow) {
this.Ticket = '';
}
},

ChangeWl: function (storeid) {
this.WlShow = true;
for (var i in this.AllContent_list) {
if (i == storeid) {
this.ExContent_list = this.AllContent_list[i];
this.ExStoreid = i;
}
}
console.log(this.ExStoreid);
console.log(this.ExContent_list);
},

SetWl: function (value) {
console.log(value);
for (var good in this.Goods_list) {
if (good == this.ExStoreid) {
this.Goods_list[good].store_goods_freight = value;
this.WlType = value;
}
}
console.log(this.Goods_list);

this.Order_amount = this.ComputedEx();
this.WlShow = false;
},

ComAllExpress: function () {
this.AllExpress = 0;
for (var i in this.AllContent_list) {
console.log(Number(this.AllContent_list[i]['express']));
this.AllExpress = Number(this.AllExpress) + Number(this.AllContent_list[i]['express']);
}
console.log(this.AllExpress);
},

SumbutBuy: function () {
if (!this.SumbutType) return;
this.SearchLoad = true;

console.log(this.$refs.Message);
this.$refs.Message.forEach(item => {
console.log(item.attributes.storeid.nodeValue);
console.log(item.value);
if (item.value != '') {
this.PaymessageAll = this.PaymessageAll + item.attributes.storeid.nodeValue + '|' + item.value + ',';
}
});
this.Fredkg = '';
console.log(this.PaymessageAll);
for (var good in this.Goods_list) {
this.Fredkg = this.Fredkg + good + '=' + this.Goods_list[good].store_goods_freight + ',';
}
console.log(this.Fredkg);

this.$axios.post('/view/member_buy-buy_step2.html', this.$qs.stringify({
key: this.Key,//用户唯一标识
ifcart: this.IfCart,//密码
cart_id: this.Cart_id,//商品ID|数量,
address_id: this.Address_info.address_id,//地址信息ID
vat_hash: this.Vat_hash,//php验证
offpay_hash: this.Offpay_hash,//php验证
offpay_hash_batch: this.Offpay_hash_batch,//php验证
pay_name: this.PayName,//online
invoice_id: this.Invoice_id,//发票ID
voucher: this.Voucher,//优惠券模板ID|店铺ID|优惠券金额
pd_pay: 0,//余额支付 0:否 1:是
password: '',//支付密码
fcode: this.Fcode,//是否为F码商品
fc_score: this.Score,//使用积分的数量
fc_ticket: this.Ticket,//使用电子券的数量
rcb_pay: '',//是否红包支付
rpt: '',//	红包
pay_message: this.PaymessageAll,//买家留言 店铺ID|留言内容
totalprice: this.Order_amount,//总金额
fredkg: this.Fredkg,//每个店铺的运费
})).then((res) => {
if (res.data.code == 200) {
console.log(res.data.datas);
this.SearchLoad = false;
console.log(res.data.datas.payment_code);

if (res.data.datas.payment_code == 'online') {
this.Pay_sn = res.data.datas.pay_sn;
this.ShowPay();
} else if (res.data.datas.payment_code == 'jzpay') {
this.$vux.toast.show({ text: res.data.datas.message, type: 'text', width: '60%' });
setTimeout(() => {
this.$router.push('/myOrder');
}, 2000);
}
} else {
this.SearchLoad = false;
this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
}
})
.catch((err) => {
console.log(err);
this.SearchLoad = false;
})
},
ShowPay: function () {
this.$axios.post('/member_buy-pay.html', this.$qs.stringify({
key: this.Key,//用户唯一标识
pay_sn: this.Pay_sn,//订单号
})).then((res) => {
if (res.data.code == 200) {
console.log(res.data.datas);
this.Paylist = res.data.datas.pay_info.payment_list;
this.PayType = this.Paylist[0].payment_code;
this.PayShow = true;

} else {
this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
}
})
.catch((err) => {
console.log(err);
})
},
ClosePay: function () {
console.log(this.PayClose);
if (this.PayClose) {
var _this = this;
this.$vux.confirm.show({
title: '提示', content: '确认取消支付吗',
onCancel() {
_this.PayShow = true;
},
onConfirm() {
_this.$router.push('/myorder');
_this.PayShow = false;
}
})
}
}
},
beforeRouteLeave(to, from, next) {
console.log(to.name);
if (to.name != 'goodcoupon') {
sessionStorage.setItem('coupon', '');
}
next();
}
}


