import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Cell, Loading} from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";
const list = () => ['全部', '待付款', '待发货', '待收货', '待评价']

export default {
    data () {
        return {
            list2: list(),
            demo2: '全部',
            index: 0,
            state_now:0,
            eventId:0,
            order_num:this.$route.query.order_state,
            codingState:'等待您付款...',
            add_time:'',//下单时间
            logistics:"",
            stateing:0,
            Key: this.getCookie("key"),//用户唯一标识
            //Key:'6c921998c5128a47e9ac436dc7f95b4f',
            State_type: 0,  //订单状态 state_new:待付款,state_payed:待发货,state_send:待收货,state_notakes:待自提,state_noeval:待评价 , 全部传空
            Order_key: '', //订单搜索关键词(没有时传空)
            aftergoods_list: [],//商品列表
            CurrPage: 1, //当前页码
            PageSize: 20, //每页数据量
            totalCount: 0, //数据总条数
            PageCount: 0, //总页数
            order_sn:'',//订单编号
            order_state:'',//订单状态
            LoadObj: false,//是否已经加载数据
            hasMoreDataObj: false,//是否有下一页数据
            pullDownRefresh: true,//是否启用下拉功能
            pullDownRefreshObj: {
              beforeTxt: "下拉刷新",
              releaseRefreshTxt: "释放立即刷新",
              txt: "加载中...",
              threshold: 40,
              stop: 0//停止时距离父容器顶部的距离
            },
            pullUpLoad: true,//是否启用上拉功能
            pullUpLoadObj: {
              threshold: -40,
              txt: { more: "查看更多", noMore: "我是有底线的" }
            },
            fullHeight: 0,
            SearchLoad: false,
        }
  },
  components: {
    XHeader,
    Tab,
    Cell,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  //页面载入
  created: function () {
    console.log(this.Key);
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted(){
    this.state_now = this.$route.params.state;
    this.fullHeight = this.autoHeight('conent');
    this.order_id = this.$route.query.order_id;
    console.log(this.fullHeight);

    this.GetMyaftergoods();
  },
  methods: {
    jumpTocodingdetail(){
      this.$router.push({ name: 'orderDetails'});
    },
    jumpTosalesdetail(value){
      this.$router.push({ name: 'applysalesDetail',query:{goods_id:value,order_id:this.order_id,order_state:this.order_state}}); 
    },
    //下拉执行逻辑
    onPullingDown: function () {
      this.InitParams();
      this.GetMyaftergoods();
    },
    //上滑执行逻辑
    onPullingUp: function () {
      // 如果有新数据
      if (this.hasMoreDataObj) {
        this.GetMyaftergoods();
      }
    },
    //获取售后订单商品详情
    GetMyaftergoods: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_order-order_info.html',{
          params:{
            order_id: this.order_id,
            key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          let orderMsg = res.data.datas.order_info;
          this.order_sn = orderMsg.order_sn;
          this.add_time = orderMsg.add_time;
          this.order_state = orderMsg.state_desc
          //state = orderMsg.state_desc
          // if(state == 20){
          //    this.order_state = '待发货';
          // }else if(state == 30){
          //    this.order_state = '待收货';
          // }else{
          //    this.order_state = '已完成';
          // }
          this.aftergoods_list.push(...res.data.datas.order_info.goods_list);
          
          console.log(this.aftergoods_list);
          this.hasMoreDataObj = res.data.hasmore;
          // this.LoadObj = true;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
   
  }, 
}














