
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Scroll
},
name: 'regionlist',

data() {
return {
Banner_list: [], //Banner列表
Good_list: [], //商品列表
CurrPage: 1, //当前页码
PageSize: 20, //每页数据量
totalCount: 0, //数据总条数
PageCount: 0, //总页数
LoadObj: false,//是否已经加载数据
hasMoreDataObj: false, //是否有下一页数据
pullDownRefresh: false, //是否启用下拉功能
pullDownRefreshObj: {
beforeTxt: "下拉刷新",
releaseRefreshTxt: "释放立即刷新",
txt: "加载中...",
threshold: 40,
stop: 0 
},
pullUpLoad: true, //是否启用上拉功能
pullUpLoadObj: {
threshold: -40,
txt: { more: "查看更多", noMore: "我是有底线的" }
},
Adtype: '', //京东淘宝类型
Adname: '', //京东淘宝名字
Adid: '', //ID
FavoritesId: '', //淘宝id
fullHeight: 0,
SearchLoad: false,
}
},

created: function() {
console.log(this.$route.query.id);
this.Adid = this.$route.query.id;
this.FavoritesId = this.$route.query.favid;
this.Adtype = this.$route.query.type;
this.GetGoodList();
},

mounted: function() {
this.fullHeight = this.autoHeight('conent');
console.log(this.fullHeight);
},

methods: {

InitParams: function() {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Good_list = []; 

this.hasMoreDataObj = false; 
},

onPullingDown: function() {
this.InitParams();
this.GetGoodList();
},

onPullingUp: function() {

if (this.hasMoreDataObj) {
this.GetGoodList();
}
},

GetGoodList: function() {
if (this.Adtype == 1) {
this.Adname = '淘宝';
if (this.Banner_list.length == 0) {
this.GetTBBannerList();
}
this.GetTBGoodList();
} else if (this.Adtype == 2) {
this.Adname = '京东';
console.log(this.Banner_list.length);
if (this.Banner_list.length == 0) {
this.GetJDBannerList();
}
this.GetJDGoodList();
}
},

GetTBGoodList: function() {
this.SearchLoad = true;
this.$axios.get('/goods-taobao_list.html', {
params: {
curpage: this.CurrPage,

page: this.PageSize,
favoritesId: this.FavoritesId,
adid: this.Adid,
}
}).then((res) => {
if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);
console.log(this.CurrPage);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = true;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetJDGoodList: function() {
this.SearchLoad = true;
this.$axios.get('/goods-jd_list.html', {
params: {
curpage: this.CurrPage,

page: this.PageSize,
favoritesId: this.Adid,
adid: this.Adid,
}
}).then((res) => {
if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);
console.log(this.Good_list);
console.log(this.CurrPage);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = true;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetTBBannerList: function() {
this.$axios.get('/index-tbkindexajax.html', {
params: {
ad: this.Adid,
}
}).then((res) => {
if (res.data.code == 200) {
this.Banner_list = res.data.datas;
console.log(this.CurrPage);
}
})
.catch((err) => {
console.log(err);
})
},

GetJDBannerList: function() {
this.$axios.get('/index-jdindexajax.html', {
params: {
favoritesId: this.Adid,
}
}).then((res) => {
if (res.data.code == 200) {
this.Banner_list = res.data.datas;
console.log(this.CurrPage);
}
})
.catch((err) => {
console.log(err);
})
},
},
}


