
import Vue from 'vue';
import Alert from 'vux/src/components/alert/index.vue'

import { SSL_OP_EPHEMERAL_RSA } from 'constants';

export default {
components: {
Alert,
},
name: 'search',

data() {
return {
Sea_list: [], //默认搜索
His_list: [], //历史记录
Hot_list: [], //热门搜索
sel: true,
sel_text: "商品",
sel_type: 0,
type_show: 1, //是否显示
SearchText: this.$route.query.searchtext == undefined ? '' : this.$route.query.searchtext, //搜索内容
SearchType: this.$route.query.page, //搜索类型
}
},

created: function () {
console.log(this.SearchType);
console.log(this.$route.query.searchtext);
if (this.SearchType == 'home' || this.SearchType == 'goodlist' || this.SearchType == 'barterzone' || this.SearchType == 'classfiy') {
this.sel_type = 0;
this.sel_text = '商品';
this.GetHomePageHisList();
this.GetHomePageHotList();
} else if (this.SearchType == 'shoplist') {
this.sel_type = 1;
this.sel_text = '店铺';
this.GetHomePageHisList();
this.GetHomePageHotList();
} else if (this.SearchType == 'find') {
this.sel_type = 0;
this.sel_text = '平台';
this.GetfundHisList();
this.GetfundHotList();
}
},

mounted: function () {

},

methods: {
ChangeSeltype: function () {
if (this.type_show == 0) {
this.type_show = 1;
} else {
this.type_show = 0;
}
},
type_clic: function (type, text) {
this.sel_type = type;
this.sel_text = text;
this.type_show = 0;
console.log(this.sel_type);
console.log(this.sel_text);
},

GetfundHisList: function () {
this.$axios.get('/fund-fund_key_list.html', {
params: {}
}).then((res) => {
if (res.data.code == 200) {
this.Sea_list = res.data.datas.list;
this.His_list = res.data.datas.his_fund_list;
console.log('发现最近搜索');
console.log(this.Sea_list);
console.log(this.His_list);
}
})
.catch((err) => {
console.log(err);
})
},

GetfundHotList: function () {
this.$axios.get('/fund-search_hot_info.html', {
params: {}
}).then((res) => {
if (res.data.code == 200) {
this.Hot_list = res.data.datas.hot_info;
if (this.Hot_list != null) {
if (this.SearchText == '') {
this.SearchText = this.Hot_list.name;
}
}
console.log('发现热门搜索');
console.log(this.Hot_list);
}
})
.catch((err) => {
console.log(err);
})
},

GetHomePageHisList: function () {
this.$axios.get('/index-search_key_list.html', {
params: {}
}).then((res) => {
if (res.data.code == 200) {
this.Sea_list = res.data.datas.list;
this.His_list = res.data.datas.his_list;
console.log('首页和列表页历史记录');
console.log(this.His_list);
}
})
.catch((err) => {
console.log(err);
})
},

GetHomePageHotList: function () {
this.$axios.get('/index-search_hot_info.html', {
params: {}
}).then((res) => {
if (res.data.code == 200) {
this.Hot_list = res.data.datas.hot_info;
if (this.Hot_list != null) {
if (this.SearchText == '') {
this.SearchText = this.Hot_list.name;
}
}
console.log('首页和列表页热门搜索');
console.log(this.Hot_list);
}
})
.catch((err) => {
console.log(err);
})
},

GetHomePageList: function () {
if (this.sel_type == 0) {
this.$router.push('/product/goodlist?keyword=' + this.SearchText + '&type=' + this.sel_type + '');
} else {
this.$router.push('/shoplist?keyword=' + this.SearchText + '&type=' + this.sel_type + '');
}

},

GetZonePageList: function () {
this.$router.push('/barterzone?keyword=' + this.SearchText + '');
},

GetfundList: function () {
console.log(this.SearchText);
console.log(this.sel_type);
if (this.SearchText == undefined) {
this.SearchText = '';
}
this.$router.push('/find?keyword=' + this.SearchText + '&qstate=' + this.sel_type + '');
},

SeachList: function () {
if (this.SearchType == 'find') {
this.GetfundList();
} else if (this.SearchType == 'home' || this.SearchType == 'shoplist' || this.SearchType == 'goodlist' || this.SearchType == 'classfiy') {
this.GetHomePageList();
} else if (this.SearchType == 'barterzone') {
this.GetZonePageList();
}
},

SeachListClick: function (value) {
if (this.SearchType == 'find') {
this.SearchText = value;
this.GetfundList();
} else if (this.SearchType == 'home' || this.SearchType == 'shoplist' || this.SearchType == 'goodlist' || this.SearchType == 'classfiy') {
this.SearchText = value;
console.log(value);
this.GetHomePageList();
} else if (this.SearchType == 'barterzone') {
this.SearchText = value;
console.log(value);
this.GetZonePageList();
}
},

DelHisSearch: function () {
if (this.SearchType == 'find') {
this.delCookie("hisFundSearch");
} else {
this.delCookie("hisSearch");
}
this.His_list = [];
},
},
}


