export default {
    name: "express",
    data() {
        return {
            Key: this.getCookie("key"),
            //用户唯一标识
            // Key: '3fc96063e73705021471b7548a8cb675',
            // Key: "6ca9f3434c210705c756126cd5bb6d78",
            //Key: "207144cf80061938e9e191b47af75b72",
            id: this.$route.query.order_id,
            expresslist: [],
            expressname: '',
            expresscode: '',

        };
    },
    created() {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        this.getexpress();
    },
    methods: {
        getexpress() {
            this.$axios.post('/member_order-search_deliver.html', this.$qs.stringify({
                key: this.Key,
                order_id: this.id,
            })).then((res) => {
                console.log(res)
                if (res.data.code == 200) {

                    this.expressname = res.data.datas.express_name;
                    this.expresscode = res.data.datas.shipping_code;
                    this.expresslist = res.data.datas.deliver_info;
                }
            })
                .catch((err) => {

                    console.log(err);
                })
        }
    }
}


