
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import bottom from "./../../views/home/bottom.vue";
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Alert,
Swiper,
SwiperItem,
bottom,
Scroll
},
name: 'find',

data() {
return {
Good_list: [],//商品列表
Page_list: [],//商品列表
Slide_list: [],//轮播图
Recommend_list: [],//特价推荐
CurrPage: 1, //当前页码
PageSize: 20, //每页数据量
totalCount: 0, //数据总条数
PageCount: 0, //总页数
LoadObj: false,//是否已经加载数据
hasMoreDataObj: false,//是否有下一页数据
pullDownRefresh: false,//是否启用下拉功能
pullDownRefreshObj: {
beforeTxt: "下拉刷新",
releaseRefreshTxt: "释放立即刷新",
txt: "加载中...",
threshold: 40,
stop: 0
},
pullUpLoad: true,//是否启用上拉功能
pullUpLoadObj: {
threshold: -40,
txt: { more: "查看更多", noMore: "我是有底线的" }
},
Keyword: '',//分类名称(获取全部商品时不传)
Qstate: '',//类型
fullHeight: 0,
SearchLoad: false,
FindType: 0,
}
},

created: function () {
this.GetRecommendList();
console.log(this.$route.query.keyword);
console.log(this.$route.query.qstate);
if (this.$route.query.keyword != undefined) {
this.FindType = 1;
this.Keyword = this.$route.query.keyword == undefined ? '' : this.$route.query.keyword;
this.Qstate = this.$route.query.qstate;
this.GetGoodList();
this.GetPageList();
} else {
this.FindType = 0;
this.GetGoodList();
}
},

mounted: function () {
this.fullHeight = this.autoHeight('conent');
console.log(this.fullHeight);

},

methods: {

handleScroll(e) {
var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
console.log(scrollTop);
},

InitParams: function () {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Good_list = [];
this.Page_list = [];
this.hasMoreDataObj = false;
},

GetGoodList: function () {
this.SearchLoad = true;
this.$axios.post('/fund-index.html', this.$qs.stringify({
})
).then((res) => {
if (res.data.code == 200) {
console.log(res.data.datas);
if (this.FindType == 0) {
this.Page_list = res.data.datas.goods_list;
this.hasMoreDataObj = false;

}
this.Slide_list = res.data.datas.mb_slide;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

onPullingDown: function () {
this.InitParams();

if (this.FindType == 0) {
this.GetGoodList();
} else {
this.GetPageList();
}
},

onPullingUp: function () {

if (this.hasMoreDataObj) {
this.GetPageList();
}
},

GetPageList: function () {
this.SearchLoad = true;
this.$axios.post('/fund-search.html', this.$qs.stringify({
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
qstate: this.Qstate,
keyword: this.Keyword,
})
).then((res) => {
if (res.data.code == 200) {
this.Page_list.push(...res.data.datas.goods_list);
console.log(this.CurrPage);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;

this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetRecommendList: function () {

this.$axios.post('/fund-recommend.html', this.$qs.stringify({
})
).then((res) => {
if (res.data.code == 200) {
this.Recommend_list = res.data.datas.goods_list;
console.log(this.Recommend_list);

}
})
.catch((err) => {

console.log(err);
})
},

GoBanner: function (link) {
if (link.indexOf("http") != -1) {
window.location.href = link;
} else {

this.$router.push(link);
}
},
},
}


