import Bscroll from "better-scroll";
import { getRect } from "../../common/js/dom";

const COMPONENT_NAME = "scroll";
export default {
  name: COMPONENT_NAME,
  props: {
    hasMoreData:{
      type: Boolean,
      default: false
    },
    probeType: {
      type: Number,
      default: 1
    },
    click: {
      type: Boolean,
      default: true
    },
    listenScroll: {
      type: Boolean,
      default: true
    },
    listenBeforeScroll: {
      type: Boolean,
      default: false
    },
    listenScrollEnd: {
      type: Boolean,
      default: false
    },
    scrollbar: {
      type: null,
      default: false
    },
    LoadObj: {
      type: Boolean,
      default: false
    },
    pullDownRefresh: {
      type: Boolean,
      default: false
    },
    pullDownRefreshObj:{
      type:null,
      default:false
    },
    pullUpLoad: {
      type: null,
      default: false
    },
    startY: {
      type: Number,
      default: 0
    },
    refreshDelay: {
      type: Number,
      default: 5
    },
  },
  data() {
    return {
      beforePullDown: true,
      loadDataPullDown:false,
      isRebounding: false,
      isPullingDown: false,
      isPullUpLoad: false,
      pullUpDirty: true
    };
  },
  computed: {
    pullUpTxt() {
      const moreTxt = this.pullUpLoad && this.pullUpLoad.txt.more;
      const noMoreTxt = this.pullUpLoad && this.pullUpLoad.txt.noMore;
      return this.hasMoreData ? moreTxt : noMoreTxt;
    },
    beforeRefreshTxt() {
      return this.pullDownRefreshObj && this.pullDownRefreshObj.beforeTxt;
    },
    releaseRefreshTxt(){
       return this.pullDownRefreshObj && this.pullDownRefreshObj.releaseRefreshTxt;
    },
    refreshTxt() {
      return this.pullDownRefreshObj && this.pullDownRefreshObj.txt;
    }
  },
  created() {
  },
  mounted() {
    setTimeout(() => {
      console.log(111);
      this.initScroll();
    }, 20);
  },
  destroyed() {
    this.$refs.scroll && this.$refs.scroll.destroy();
  },
  methods: {
    initScroll() {
      if (!this.$refs.scrollwrapper) {
        return;
      }
      let options = {
        probeType: this.probeType,
        click: this.click,
        pullDownRefresh: this.pullDownRefresh,
        pullDownRefreshObj:this.pullDownRefreshObj,
        pullUpLoad: this.pullUpLoad
      };
      //初始化的是指定元素的第一个子元素
      this.scroll = new Bscroll(this.$refs.scrollwrapper, options);
      if (this.listenScroll) {
        this.scroll.on("scroll", pos => {
         if (this.pullDownRefresh && pos.y > 40)
         {
            this.loadDataPullDown=true;
            this.testData=true;
         }
        });
      }

      if (this.listenScrollEnd) {
        this.scroll.on("scrollEnd", pos => {
          this.$emit("scroll-end", pos);
        });
      }

      if (this.listenBeforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScrollStart");
        });

        this.scroll.on("scrollStart", () => {
          this.$emit("scroll-start");
        });
      }

      if (this.pullDownRefresh) {
        this._initPullDownRefresh();
      }

      if (this.pullUpLoad) {
        this._initPullUpLoad();
      }
    },
    disable() {
      this.scroll && this.scroll.disable();
    },
    enable() {
      this.scroll && this.scroll.enable();
    },
    refresh() {
      this.scroll && this.scroll.refresh();
    },
    clickItem(e, item) {
      // console.log(e);
      // this.$emit("click", item);
    },
    destroy() {
      this.scroll.destroy();
    },
    forceUpdate(dirty) {
      if (this.pullDownRefresh && this.isPullingDown) {
        this.isPullingDown = false;
        this._reboundPullDown().then(() => {
          this._afterPullDown();
        });
      } else if (this.pullUpLoad && this.isPullUpLoad) {
        this.isPullUpLoad = false;
        this.scroll.finishPullUp();
        this.pullUpDirty = dirty;
        this.refresh();
      } else {
        this.refresh();
      }
    },
    _initPullDownRefresh() {
      this.scroll.on("pullingDown", () => {
        this.beforePullDown = false;
        this.isPullingDown = true;
        this.$emit("pullingDown");
        this.forceUpdate(true);
      });
    },
    _initPullUpLoad() {
       this.scroll.on("pullingUp", () => {
        this.isPullUpLoad = true;
        this.$emit("pullingUp");
        this.forceUpdate(true);
      });
    },
    _reboundPullDown() {
      const { stopTime = 10 } = this.pullDownRefresh;
      return new Promise(resolve => {
        setTimeout(() => {
          this.isRebounding = true;
          this.scroll.finishPullDown();
          resolve();
        }, stopTime);
      });
    },
    _afterPullDown() {
      setTimeout(() => {
        this.beforePullDown = true;
        this.isRebounding = false;
        this.loadDataPullDown=false;
        this.refresh();
      }, this.scroll.options.bounceTime);
    },
    scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
  }
};



