import Vue from 'vue';
import { XInput, Group, XButton, XHeader, PopupHeader, Popup, Picker, Radio, TransferDom, Cell, Selector } from 'vux';
export default {
	components: {
		XInput,
		XButton,
		Group,
		XHeader,
		Picker, 
		PopupHeader, 
		Popup,
		Radio,
		Cell,
		Selector 
	},
	name: 'revisePho',
	directives: {
		TransferDom
	  },
	data() {
		return {
			img_code: '',//图形验证码
			pho_code: '',//手机验证码
			img_code_url_k: '',//获取图形验证码图片地址url的key
			img_code_url: null,//图形验证码图片地址url
			err: '',
			express_cord:'',
			title: '退单详情',
			show1:false,
			value:'',
			left:'取消',
			list:[],
			return_id:0,
			right:'确定',
			title2:'请选择一家快递',
			key:this.getCookie("key"),
			refund_id:this.$route.query.refund_id,
			time: 0,
			refund_sn:this.$route.query.refund_sn,
			express:[],//快递列表
			show: true,
			but_style: {
				background: '#dadada',
				height: '25px',
				lineHeight: '25px',
			},
			mobile: '',//当前用户手机号码
			pho_code_url: '',//手机验证码获取url
			but_smb_url: ''//点击下一步url地址
		}
	},
	//页面载入
	created: function () {
	  if (this.Key == '') {
		this.$router.push('/login');
	  }
	},
	//初始化数据
	mounted: function () {
         this.getExpresslist();
	},
    change (value) {
				console.log('new Value', value)
	  },
	methods: {
		//获取快递列表
		getExpresslist(){
			this.$axios.get('/member_return-ship_form.html',{
				params:{
					key: this.key,
					refund_id: this.refund_id,
				} 
			 }).then((res) => {
			   if (res.data.code == 200) {
					 //this.list.push(res.data.datas.express_list);
					 this.list = [];
					 this.list.push(...res.data.datas.express_list);
					 console.log(this.list)
			   }
			 })
			   .catch((err) => {
				 console.log(err);
			   })
		},
		onChange (val) {
			console.log(val)
			return val;
		},
		//退货发货点击完成按钮	
		next(){
			this.$axios.post('/member_return-ship_post.html', this.$qs.stringify({
				return_id: this.refund_id,
				key: this.key,
				express_id:this.value,
				invoice_no:this.express_cord

			})
			).then((res) => {
					this.$vux.alert.show({
						title: '成功',
						content: '提交成功'
					})
					if (res.data.code == 200) {
							this.SearchLoad = false;
							this.DelType = 1;
							this.DelNum--;
					}
			})
					.catch((err) => {
							this.SearchLoad = false;
							console.log(err);
					})
			}
	 }
	 
}


