import Vue from 'vue';
import { XInput, Group, XButton, XHeader } from 'vux';
export default {
	components: {
		XInput,
		XButton,
		Group,
		XHeader
	},
	name: 'revisePho',
	data() {
		return {
			img_code: '',//图形验证码
			pho_code: '',//手机验证码
			code_text: "获取验证码",
			img_code_url_k: '',//获取图形验证码图片地址url的key
			img_code_url: null,//图形验证码图片地址url
			err: '',
			title: '修改手机验证',
			time: 0,
			show: true,
			but_style: {
				background: '#f16565',
        		color: '#fff',
				height: '25px',
				lineHeight: '25px',
			},
			mobile: '',//当前用户手机号码
			pho_code_url: '',//手机验证码获取url
			but_smb_url: '',//点击下一步url地址
			Key: this.getCookie("key"),
		}
	},
	//页面载入
	created: function () {
		if (this.Key == '') {
			this.$router.push('/login');
		}
	},
	//初始化数据
	mounted: function () {
		if (this.$route.query.type == 1) {
			this.pho_code_url = '/member_account-modify_mobile_step2.html';
			this.but_smb_url = '/member_account-modify_mobile_step3.html';
			this.title = '修改手机验证';
			this.show = false;
		} else {
			this.pho_code_url = '/member_account-modify_paypwd_step2.html';
			this.but_smb_url = '/member_account-modify_paypwd_step3.html';
			this.title = '设置支付密码';
			this.show = true;
		}
		//获取当前用户手机号
		this.$axios.get('/member_account-get_mobile_info.html', {
			params: { key: this.Key } //"6c921998c5128a47e9ac436dc7f95b4f" this.getCookie("key"),
		}).then((res) => {
			if (res.data.code == "200") {
				this.mobile = res.data.datas.mobile;
				console.log(this.mobile)
			}
		}).catch((err) => {
			console.log(err);
		})
		//获取图形验证码图片地址
		this.get_pic_code();
	},

	methods: {
		//获取图形验证码图片地址
		get_pic_code() {
			this.$axios.get('/seccode-makecodekey.html', {}).then((res) => {
				if (res.data.code == "200") {
					this.img_code_url_k = res.data.datas.codekey;
					this.img_code_url = this.$globalScope.root + '/seccode-makecode.html?k=' + res.data.datas.codekey + '&t=' + Math.random();
				}
			}).catch((err) => {
				console.log(err);
			})
		},
		//点击图形验证码
		img_code_cli() {
			this.get_pic_code();
		},
		//手机验证码
		code_text_cli() {
			if (this.time == 0) {
				this.$axios.post(this.pho_code_url, this.$qs.stringify({
					key: this.Key,//"6c921998c5128a47e9ac436dc7f95b4f",
					captcha: this.img_code,
					codekey: this.img_code_url_k
				})
				).then((res) => {
					if (res.data.code == 200) {
						//显示倒计时
						this.but_style.background = "#ccc";
						clearInterval(this.interval);
						this.time = res.data.datas.sms_time;
						this.code_text = this.time + "秒后重试";
						this.interval = setInterval(() => {
							if (this.time > 0) {
								this.time--
								this.code_text = this.time + "秒后重试";
							} else {
								this.code_text = "获取验证码";
								this.but_style.background = "#f16565";
								clearInterval(this.interval);
							}
						}, 1000)
					} else {
						this.$vux.alert.show({
							title: '失败',
							content: res.data.datas.error
						})
					}
				})
					.catch((err) => {
						console.log(err);
					})
			}
		},
		//下一步
		next() {
			this.$axios.post(this.but_smb_url, this.$qs.stringify({
				key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
				auth_code: this.pho_code
			})
			).then((res) => {
				if (res.data.code == 200) {
					if (this.$route.query.type == 1) {
						this.$router.push({ path: '/revisePho_confirm' });
					} else {
						this.$router.push({ path: '/revisePayPwd' });
					}
				} else {
					this.$vux.alert.show({
						title: '失败',
						content: res.data.datas.error
					})
				}
			})
				.catch((err) => {
					console.log(err);
				})
		}
	}
}


