import Vue from 'vue';
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
components: {
XButton,
Group,
XHeader,
XTextarea
},
name: 'revisePayPwd',
data() {
return {
value: '',
Key: this.getCookie("key"),
}
},

created: function () {
if (this.Key == '') {
this.$router.push('/login');
}
},
methods: {
subm() {
if(this.value == ''){
this.$vux.alert.show({
title: '失败',
content: "请输入反馈信息"
})
}else{
this.$axios.post("/member_feedback-feedback_add.html", this.$qs.stringify({
key: this.getCookie("key"),//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
feedback: this.value
})
).then((res) => {
if (res.data.code == 200) {
this.$vux.alert.show({
title: '成功',
content: "提交成功"
})
} else {
this.$vux.alert.show({
title: '失败',
content: res.data.datas.error
})
}
})
.catch((err) => {
console.log(err);
})
}
}
},
}


