import Vue from 'vue';
import { XInput, Group, XButton, Checker, XHeader } from 'vux';
export default {
    components: {
        XInput,
        XButton,
        Group,
        XHeader
    },
    name: 'revisePayPwd',
    data() {
        return {
            pho: '',
            pho2: '',
            title: '设置支付密码',
            hasNoPower: true,//无设置修改权限
        }
    },
    //初始化页面数据
    mounted: function () {
        this.$axios.get('/member_account-modify_paypwd_step4.html', {
            params: { key: this.getCookie("key") }
        }).then((res) => {
            if (res.data.code == "200") {
                this.hasNoPower = false;
            }
            else {
                this.$vux.alert.show({
                    title: '失败',
                    content: "权限不足或操作超时"
                })
            }
        }).catch((err) => {
            console.log(err);
        })
    },
    methods: {
        //保存支付密码
        save() {
            if (this.hasNoPower) {
                this.$vux.alert.show({
                    title: '失败',
                    content: "权限不足或操作超时"
                })
            }
            else if (this.pho !== this.pho2) {
                this.$vux.alert.show({
                    title: '失败',
                    content: "密码不一致"
                })
            }
            else {
                this.$axios.post("/member_account-modify_paypwd_step5.html", this.$qs.stringify({
                    key: this.getCookie("key"),//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
                    password: this.pho,
                    password1: this.pho2
                })
                ).then((res) => {
                    if (res.data.code == 200) {
                        this.$vux.alert.show({
                            title: '成功',
                            content: "设置成功"
                        })
                        setTimeout(() => {
                            // location.href = this.ApiUrl + '/my';
                            this.$router.push('/my');
                        }, 2000);
                    } else {
                        this.$vux.alert.show({
                            title: '失败',
                            content: res.data.datas.error
                        })
                    }
                })
                    .catch((err) => {
                        console.log(err);
                    })
            }
        },
    }
}  


