import Vue from 'vue';
import { XHeader, Cell, Group } from 'vux';
export default {
	components: {
		XHeader,
		Cell,
		Group
	},
	data() {
		return {
			username: this.getCookie("username"),
			avatar: this.getCookie("avatar"),
			member_level: this.getCookie("member_level"),
			Key: this.getCookie("key"),
			setInfo:""//是否需要设置密码
		}
	},
	name: 'personalSet',
	//页面载入
	created: function () {
	  if (this.Key == '') {
		this.$router.push('/login');
	  }
	},
	//数据初始化 是否需要设置密码 
	mounted: function ($routey) {
		this.$axios.get('/member_account-get_paypwd_info.html', {
			params: { key: this.Key }
		}).then((res) => {
			if (res.data.code == 200) {
				if (!res.data.datas.state) {
            		this.setInfo='未设置';
            	}
			}
		}).catch((err) => {
			console.log(err);
		})
	},
	methods: {
		Gotoaddress() {
			this.$router.push({
				name: 'addressAdministration',
			});
		},
		Gotosetpaypsd(value) {
			this.$router.push({ name: 'revisePho', query: { type: value } });
		},
		Gotoclearstorage() {
		},
		Gotoabout() {

		},
		quit() {
			this.$axios.post("/logout-index.html", this.$qs.stringify({
				key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
				"username": this.getCookie("username"),
				"client": "wap"
			})
			).then((res) => {
				if (res.data.code == 200) {
					this.delAllCookie();
					this.$router.push({ path: '/login' });
				} else {
					this.$vux.alert.show({
						title: '失败',
						content: res.data.datas.error
					})
				}
			})
				.catch((err) => {
					console.log(err);
				})
		}
	}
}


