import Vue from 'vue';
import { XHeader, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      TitleName: '我的优惠券',//页面名字
      TitleType: 0,//页面类型
      UncouponType: true,//是否显示查看已失效优惠券
      Coupon_list: [], //优惠券列表
      CurrPage: 1, //当前页码
      PageSize: 20, //每页数据量
      totalCount: 0, //数据总条数
      PageCount: 0, //总页数
      LoadObj: false, //是否已经加载数据
      hasMoreDataObj: false, //是否有下一页数据
      pullDownRefresh: false, //是否启用下拉功能
      pullDownRefreshObj: {
        beforeTxt: "下拉刷新",
        releaseRefreshTxt: "释放立即刷新",
        txt: "加载中...",
        threshold: 40,
        stop: 0 //停止时距离父容器顶部的距离
      },
      pullUpLoad: true, //是否启用上拉功能
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "查看更多", noMore: "我是有底线的" }
      },
      Key: this.getCookie("key"),//用户唯一标识
      voucher_state: 1,
      noMore:'',//失效券没有时提示
      UsdCount: 0,//未使用数量
      UsdingCount: 0,//已使用数量
      ifhas:false,
    }
  },
  components: {
    XHeader,
    Loading,
    Scroll
  },
  name: 'coupon',
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetCouponCount(1);
    this.GetCouponCount(2);
    this.GetCouponList();
  },
  mounted() {

  },
  filters: {
      ChangeTime(value) {
          console.log(value)
          if (value != undefined) {
              value = value.replace('-', '.');
              value = value.replace('-', '.');

              var newdate = value;
              return newdate;
          }
      },
  },
  methods: {
    type_clic(type) {
      this.voucher_state = type;
    },//初始化数据
    InitParams: function () {
      this.CurrPage = 1; //当前页码
      this.totalCount = 0; //数据总条数
      this.PageCount = 0; //总页数
      this.Coupon_list = []; //优惠券列表
      this.hasMoreDataObj = false; //是否有下一页数据
    },
    //下拉执行逻辑
    onPullingDown: function () {
      this.InitParams();
      this.GetCouponCount(1);
      this.GetCouponCount(2);
      this.GetCouponList();
    },
    //上滑执行逻辑
    onPullingUp: function () {
      // 如果有新数据
      if (this.hasMoreDataObj) {
        this.GetCouponList();
      }
    },
    //切换标签
    ChangeType: function (type) {
      if (type == '1' || type == '2') {
        this.TitleName = '我的优惠券';//页面名字
        this.TitleType = 0;//页面类型
        this.UncouponType = true;//是否显示查看已失效优惠券
        this.ifhas = false;
      } else if (type == '3|4') {
        this.TitleName = '失效券';//页面名字
        this.TitleType = 1;//页面类型
        this.UncouponType = false;//是否显示查看已失效优惠券
        this.ifhas = true;
      }
      this.voucher_state = type;
      this.InitParams();
      this.GetCouponList();
    },
    //获取优惠券列表
    GetCouponList: function () {
      this.SearchLoad = true;
      // this.$axios.post('/member_voucher-voucher_list.html', this.$qs.stringify({
      //   curpage: this.CurrPage,
      //   page: this.PageSize,
      //   voucher_state: this.voucher_state,
      //   key: this.Key,
      // })
      this.$axios.get('/member_voucher-voucher_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          voucher_state: this.voucher_state,
          key: this.Key,
        }
      }
      ).then((res) => {
        if (res.data.code == 200) {
          console.log(res.data.datas.voucher_list)
          res.data.datas.voucher_list.forEach(item => {
            if (item.voucher_state == 1) {
              this.$set(item, "couponclass", 'one');
            } else if (item.voucher_state == 2) {
              this.$set(item, "couponclass", 'ac');
            } else if (item.voucher_state == 3) {
              this.$set(item, "couponclass", 'over');
            } else if (item.voucher_state == 4) {
              this.$set(item, "couponclass", 'back');
            }
          })
          this.Coupon_list.push(...res.data.datas.voucher_list);
          console.log(res.data.datas);
          console.log(this.Coupon_list);
          // this.totalCount = res.data.datas.count;
          this.PageCount = res.data.page_total;
          this.CurrPage++; //当前页码
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //获取优惠券数量
    GetCouponCount: function (value) {
      // this.$axios.post('/member_voucher-voucher_list.html', this.$qs.stringify({
      //   curpage: this.CurrPage,
      //   page: this.PageSize,
      //   voucher_state: value,
      //   key: this.Key,
      // })
      this.$axios.get('/member_voucher-voucher_list.html', {
        params: {
          curpage: 1,
          page: 1,
          voucher_state: value,
          key: this.Key,
        }
      }
      ).then((res) => {
        if (res.data.code == 200) {
          console.log(res.data.page_total);
          if (value == 1) {
            this.UsdCount = res.data.page_total;
          } else if (value == 2) {
            this.UsdingCount = res.data.page_total;
          }
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },
    //返回
    GoBack: function () {
      if (this.TitleType == 0) {
        this.$router.go(-1);
      } else if (this.TitleType == 1) {
        this.TitleName = '我的优惠券';//页面名字
        this.TitleType = 0;//页面类型
        this.UncouponType = true;//是否显示查看已失效优惠券
        this.voucher_state = 1;
        this.ifhas = false;
        this.InitParams();
        this.GetCouponList();
      }
    },
  },
}














