export default {	
  	data(){
		return {
			tab_time:1,
		}
	},		
	name: 'bottom',
	methods: {
		tab_time_clic (type){
			this.tab_time=type
		}
	},
	mounted: function () {
		const toDepth = this.$route.path.split('/')[1];　　　
		switch(toDepth)
			{
				case "homePage":
				  this.tab_time=1;
				  break;
				case "LocalLife":
				  this.tab_time=2;
				  break;
				case "wallet":
				  this.tab_time=3;
				  break;
				case "find":
				  this.tab_time=4;
				  break;
				case "my":
				  this.tab_time=5;
				  break;
			}
		console.log(toDepth);
	}
}



