
import Vue from 'vue';
import Alert from 'vux/src/components/alert/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import bottom from "./../../views/home/bottom.vue";

export default {
components: {
Alert,
bottom,
Swiper,
SwiperItem,
},
name: 'home',

data() {
return {
Slide_list: [], //轮播图
Active_list: [], //热门活动
FeatRmd_list: [], //精品推荐
TbkCategory_list: [], //淘宝类目
TbkFavorites_list: [], //淘宝收藏
JdCategory_list: [], //京东类目
JdFavorites_list: [], //京东收藏
HomeType: 0, //首页类型 0：第一书记 1：淘宝 2：京东 3：携程 4：易货专区
TitleName: '第一书记',
Key: this.getCookie("key"), //用户唯一标识
}
},

created: function() {
this.GetPopActiveList();
this.GetFeatRmdList();
},

mounted: function() {

},

methods: {

GetPopActiveList: function() {
this.$axios.get('/index-index.html', {
params: {

}
}).then((res) => {
if (res.data.code == 200) {
if (res.data.datas.length > 0) {
for (var i = 0; i < res.data.datas.length; i++) {
if (res.data.datas[i].type == 'mb_slide') {
this.Slide_list = res.data.datas[i].array;
console.log("轮播图");
console.log(this.Slide_list);
}
if (res.data.datas[i].type == 'tbk_category_list') {
this.TbkCategory_list = res.data.datas[i].array;
console.log("淘宝类目");
console.log(this.TbkCategory_list);
}
if (res.data.datas[i].type == 'tbk_favorites_list') {
this.TbkFavorites_list = res.data.datas[i].array;
console.log("淘宝收藏");
console.log(this.TbkFavorites_list);
}
if (res.data.datas[i].type == 'jd_category_list') {
this.JdCategory_list = res.data.datas[i].array;
console.log("京东类目");
console.log(this.JdCategory_list);
}
if (res.data.datas[i].type == 'jd_favorites_list') {
this.JdFavorites_list = res.data.datas[i].array;
console.log("京东收藏");
console.log(this.JdFavorites_list);
}
if (res.data.datas[i].type == 'dysj') {
this.Active_list = res.data.datas[i].array;
console.log("第一书记热门活动");
console.log(this.Active_list);
}
}
}
}
})
.catch((err) => {
console.log(err);
})
},

GetFeatRmdList: function() {
this.$axios.post('/index-goodsRecommend.html', this.$qs.stringify({

})).then((res) => {
if (res.data.code == 200) {
this.FeatRmd_list = res.data.datas;
console.log("精品推荐");
console.log(this.FeatRmd_list);
}
})
.catch((err) => {
console.log(err);
})
},

Changetype: function(value) {
this.GetPopActiveList();
if (value == 0) {
this.GetFeatRmdList();
this.HomeType = 0;
this.TitleName = '第一书记';
} else if (value == 1) {
this.HomeType = 1;
this.TitleName = '淘宝';
} else if (value == 2) {
this.HomeType = 2;
this.TitleName = '京东';
}
},

GoRegionlist: function(id, favid, type) {
this.$router.push({
name: 'regionlist',
query: {
id: id,
favid: favid,
type: type,
}
});
},

GoBanner: function(link) {
if (link.indexOf("http") != -1) {
window.location.href = link;
} else {

this.$router.push(link);
}
},

AddCart: function(goodsid) {
console.log(this.Key);
if(this.Key==''){
this.$vux.toast.show({ text: '请登录', type: 'text' });
return;
}
this.$axios.post('/member_cart-cart_add.html', this.$qs.stringify({
key: this.Key,
goods_id: goodsid,
quantity: 1,
})).then((res) => {
if (res.data.code == 200) {
if (res.data.datas == 1) {
this.$vux.toast.show({ text: '加入购物车成功', type: 'text' });
} else {
this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
}
}
})
.catch((err) => {
this.$vux.toast.show({ text: '加入购物车失败', type: 'text' });
})
},

gonextpage(goodid, soreid, coupon) {

if (coupon) {
this.$router.push({
name: 'couponCenter',
query: {
goods_id: goodid,
store_id: soreid
}
});
} else {
this.$router.push({
name: 'gooddetail',
query: {
goodsid: goodid,
}
});

}

},




},
}


