import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Cell, Rater, Group, XTextarea, Loading, } from 'vux'

export default {
  data() {
    return {
      demo2: '全部',
      evalindex: '0',//好评5、4中评3、2差评1
      store_servicecredit: 0,//服务态度
      store_desccredit: 0,//商品描述
      store_deliverycredit: 0,//物流服务
      comment: '',//评价
      agree: true,//是否匿名
      picUrl: '',
      img: `<img src="${require("../../../assets/start.png")}">`,
      key: this.getCookie("key"),
      order_id: this.$route.query.orderid,//订单ID
      goods_id: this.$route.query.goodsid,//商品ID
      goods: [],//商品评价信息

      imgnum: 4,//上传图片限制
      SearchLoad: false,
      refund_pic: [],
      value: "",
      orderIndex: null,
      status: 'ready',
      files: [],
      point: {},
      percent: 0
    }
  },
  components: {
    XHeader,
    Tab,
    Cell,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Rater,
    Group,
    XTextarea,
    Loading,
  },
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted() {
    // this.order_id = 2262;
    // this.goods_id = 109494;
  },
  methods: {
    //选择评价
    SetEval(index, evaltext) {
      if (index == 1) {
        if (evaltext == '5、4') {
          this.evalindex = '0';
        } else {
          this.evalindex = '5、4';
        }
      } else if (index == 2) {
        if (evaltext == '3、2') {
          this.evalindex = '0';
        } else {
          this.evalindex = '3、2';
        }
      } else if (index == 3) {
        if (evaltext == '1') {
          this.evalindex = '0';
        } else {
          this.evalindex = '1';
        }
      }
    },
    //图片选择
    // fileChanged() {
    //   const singleFile = this.$refs.file.files[0];
    //   console.log(singleFile);
    //   var formdata1 = new FormData();
    //   formdata1.append('key', "6c921998c5128a47e9ac436dc7f95b4f");
    //   formdata1.append('file', singleFile, singleFile.name);
    //   let config = { headers: { 'Content-Type': 'multipart/form-data' } };
    //   this.$axios.post("sns_album-swfupload.html", formdata1, config).then((res) => {
    //     if (res.status == 200) {
    //       this.picName = res.data.file_name;
    //       this.picUrl = res.data.file_url;
    //       console.log(this.picUrl)
    //     } else {
    //       this.$vux.alert.show({
    //         title: '失败',
    //         content: res.data.message
    //       })
    //     }
    //   })
    //     .catch((err) => {
    //       console.log(err);
    //     })
    // },
    //提交
    subm() {
      if (this.evalindex == 0) {
        this.$vux.toast.show({ text: '请选择评价', type: 'text' });
        return;
      } else if (this.store_servicecredit == 0) {
        this.$vux.toast.show({ text: '请选择服务态度星级', type: 'text' });
        return;
      } else if (this.store_desccredit == 0) {
        this.$vux.toast.show({ text: '请选择商品描述星级', type: 'text' });
        return;
      } else if (this.store_deliverycredit == 0) {
        this.$vux.toast.show({ text: '请选择物流服务星级', type: 'text' });
        return;
      }

      var treeCol = new Array();
      treeCol[this.goods_id] = new Array();

      treeCol[this.goods_id]['score'] = new Array();

      treeCol[this.goods_id]['store_desccredit'] = new Array();
      treeCol[this.goods_id]['store_servicecredit'] = new Array();
      treeCol[this.goods_id]['store_deliverycredit'] = new Array();
      treeCol[this.goods_id]['evaluate_image'] = new Array();
      treeCol[this.goods_id]['comment'] = new Array();
      treeCol[this.goods_id]['agree'] = new Array();

      treeCol[this.goods_id]['score'] = this.evalindex;
      treeCol[this.goods_id]['store_desccredit'] = this.store_desccredit;
      treeCol[this.goods_id]['store_servicecredit'] = this.store_servicecredit;
      treeCol[this.goods_id]['store_deliverycredit'] = this.store_deliverycredit;
      treeCol[this.goods_id]['comment'] = this.comment;
      treeCol[this.goods_id]['agree'] = this.agree == true ? '1' : '0';

      for (var i in this.refund_pic) {
        // console.log(i);
        treeCol[this.goods_id]['evaluate_image'][i] = new Array();
        treeCol[this.goods_id]['evaluate_image'][i] = this.refund_pic[i].file_name;
      }

      // console.log(treeCol)
      this.$axios.post("/member_evaluate-save.html", this.$qs.stringify({
        key: this.key,//6c921998c5128a47e9ac436dc7f95b4f this.getCookie("key")
        order_id: this.order_id,
        goods: treeCol,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.$vux.confirm.show({
            title: '成功',
            content: '提交成功',
            onShow() {
              // console.log('plugin show')
            },
            onHide() {
              // console.log('plugin hide')
            },
            onCancel() {
              this.$router.go(-1);
              // console.log('plugin cancel')
            },
            onConfirm() {
              this.$router.go(-1);
              // console.log('plugin confirm')
            }
          })
        } else {
          this.$vux.alert.show({
            title: '失败',
            content: res.data.datas.error
          })
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },


    add() {
      if (this.refund_pic.length >= this.imgnum) {
        this.$vux.toast.show({ text: '最多上传' + this.imgnum + '张图片', type: 'text' });
        return
      }
      this.$refs.file.click();
      // console.log(this.files);
    },

    finished() {
      this.files = []
      this.status = 'ready'
    },
    remove(index) {
      // this.files.splice(index, 1)
      this.refund_pic.splice(index, 1);
    },
    fileChanged() {
      this.SearchLoad = true;
      const list = this.$refs.file.files
      for (let i = 0; i < list.length; i++) {
        if (!this.isContain(list[i])) {
          const item = {
            name: list[i].name,
            size: list[i].size,
            file: list[i]
          }
          // console.log(item.size);
          if (item.size >= 6000000) {
            this.$vux.toast.show({ text: '每张图片不能超过6M', type: 'text' });
            // return;
          } else {
            this.html5Reader(list[i], item)
            this.files.push(item)
          }
        }
      }
      console.log(this.files);
      if (this.files.length > 0) {
        const formData = new FormData()
        this.files.forEach((item) => {
          formData.append('file', item.file)
          this.files.splice(item, 1);
        })
        console.log(this.files);
        formData.append('key', this.key);
        let config = { headers: { 'Content-Type': 'multipart/form-data' } };
        //额外设置请求时间
        this.$axios.defaults.timeout = 60000;
        this.$axios.post('/sns_album-swfupload.html', formData, config)
          .then((res) => {
            // console.log(res);
            if (res.data.state != false) {
              this.refund_pic.push({
                file_id: res.data.file_id,
                file_name: res.data.file_name,
                file_url: res.data.file_url,
                origin_file_name: res.data.origin_file_name
              });
              this.SearchLoad = false;
              console.log(this.refund_pic);
              // this.$vux.toast.show({ text: '上传成功', type: 'text' });
            } else {
              this.SearchLoad = false;
              this.$vux.toast.show({ text: '上传失败', type: 'text' });
            }
          })
          .catch((err) => {
            console.log(err);
            this.SearchLoad = false;
            this.$vux.toast.show({ text: '网络不给力', type: 'text' });
          })
      }
      this.$refs.file.value = ''
    },
    // 将图片文件转成BASE64格式
    html5Reader(file, item) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.$set(item, 'src', e.target.result)
      }
      reader.readAsDataURL(file)
    },
    isContain(file) {
      return this.files.find((item) => item.name === file.name && item.size === file.size)
    },

  },
}














