
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";
import { ease } from './../../../common/js/ease';


export default {
components: {
Loading,
Alert,
Scroll
},
name: 'goodlist',

data() {
return {
Good_list: [], //商品列表
CurrPage: 1, //当前页码
PageSize: 20, //每页数据量
totalCount: 0, //数据总条数
PageCount: 0, //总页数
LoadObj: false, //是否已经加载数据
hasMoreDataObj: false, //是否有下一页数据
pullDownRefresh: false, //是否启用下拉功能
pullDownRefreshObj: {
beforeTxt: "下拉刷新",
releaseRefreshTxt: "释放立即刷新",
txt: "加载中...",
threshold: 40,
stop: 0 
},
pullUpLoad: true, //是否启用上拉功能
pullUpLoadObj: {
threshold: -40,
txt: { more: "查看更多", noMore: "我是有底线的" }
},
Title: '', //分类名称(获取全部商品时不传)
Gc_id: '', //分类ID(获取全部商品时不传)
Order: 1, //排序规则 1：正序
Key: 2, //查询规则 1:销量 2:综合 3:价格
fullHeight: 0,
SearchLoad: false,
Keyword: '', //分类名称(获取全部商品时不传)
Type: '', //商品类型
emptyshow: false,
SelType: 0
}
},

created: function () {



if (this.$route.query.keyword != undefined || this.$route.query.type != undefined) {
this.SelType = 0;
this.Keyword = this.$route.query.keyword == undefined ? '' : this.$route.query.keyword;
this.Type = this.$route.query.type;
this.GetList();
} else if (this.$route.query.title != undefined || this.$route.query.gc_id != undefined) {
this.SelType = 1;
this.Title = this.$route.query.title == undefined ? '' : this.$route.query.title;
this.Gc_id = this.$route.query.gc_id;
this.GetList();
} else {
this.SelType = 0;
this.GetList();
}


},

mounted: function () {

},

methods: {

scrollTo: function () {

this.$refs.scroll.scrollTo(0, this.$refs.scroll.startY, 700, ease['bounce']);
},

InitParams: function () {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Good_list = []; 
this.hasMoreDataObj = false; 
},

onPullingDown: function () {
this.InitParams();
this.GetList();
},

onPullingUp: function () {

if (this.hasMoreDataObj) {
this.GetList();
}
},

ChangeType: function (type) {
this.Key = type;
if (type == 3) {
if (this.Order == 1) {
this.Order = 2;
} else if (this.Order == 2) {
this.Order = 1;
}
}
this.scrollTo();
this.InitParams();
this.GetList();
},
GetList: function () {
if (this.SelType == 0) {
this.GetPageList();
} else if (this.SelType == 1) {
this.GetGoodList();
}
},

GetGoodList: function () {
this.emptyshow = false;
this.SearchLoad = true;
this.$axios.get('/goods-goods_list.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
title: this.Title,
gc_id: this.Gc_id,
order: this.Order,
key: this.Key,
}
}).then((res) => {
console.log(res)
if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);

this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;

this.SearchLoad = false;
if (this.Good_list.length == 0) {
this.emptyshow = true;
}
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetPageList: function () {
this.emptyshow = false;
this.SearchLoad = true;
this.$axios.get('/goods-goods_list.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
keyword: this.Keyword,
ex: 0,
order: this.Order,
key: this.Key,
type: this.Type,
}
}).then((res) => {

if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);

this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.SearchLoad = false;
if (this.Good_list.length == 0) {
this.emptyshow = true;
}
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},


gonextpage(goodid, soreid, coupon) {

if (coupon) {
this.$router.push({
name: 'couponCenter',
query: {
goods_id: goodid,
store_id: soreid
}
});
} else {
this.$router.push({
name: 'gooddetail',
query: {
goodsid: goodid,
}
});

}

},

},
}


