import Vue from 'vue';
import { XInput, Group, XButton, XHeader } from 'vux';
export default {
	components: {
		XInput,
		XButton,
		Group,
		XHeader
	},
	name: 'revisePho_confirm',
	data() {
		return {
			pho: '',
			title: '修改手机验证',
			img_code: '',
			pho_code: '',
			agree: true,
			code_text: "获取验证码",
			img_code_url_k: '',//获取图形验证码图片地址url的key
			img_code_url: null,//图形验证码图片地址url
			err: '',
			time: 0,
			but_style:{
				background: '#f16565',
				color: '#fff'
			},
			Key: this.getCookie("key"),
		}
	},
	//页面载入
	created: function () {
		if (this.Key == '') {
			this.$router.push('/login');
		}
	},
	//数据初始化
	mounted: function () {
		this.get_pic_code();
	},
	methods: {
		//获取图形验证码图片地址
		get_pic_code() {
			this.$axios.get('/seccode-makecodekey.html', {}).then((res) => {
				if (res.data.code == "200") {
					this.img_code_url_k = res.data.datas.codekey;
					this.img_code_url = this.$globalScope.root + '/seccode-makecode.html?k=' + res.data.datas.codekey + '&t=' + Math.random();
				}
			}).catch((err) => {
				console.log(err);
			})
		},
		//图形验证码点击
		img_code_cli() {
			this.get_pic_code();
		},
		//获取手机验证码
		code_text_cli() {
			if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
				if (this.time == 0) {
					this.$axios.post("/member_account-bind_mobile_step1.html", this.$qs.stringify({
						key: this.Key,
						mobile: this.pho,
						captcha: this.img_code,
						codekey: this.img_code_url_k
					})
					).then((res) => {
						if (res.data.code == 200) {
							//显示倒计时
							this.$vux.alert.show({
								title: '成功',
								content: '短信验证码发送成功'
							})
							clearInterval(this.interval);
							this.time = 60;
							this.code_text = this.time + "秒后重试";
							this.interval = setInterval(() => {
								if (this.time > 0) {
									this.time--
									this.code_text = this.time + "秒后重试";
								} else {
									this.code_text = "获取验证码";
									clearInterval(this.interval);
								}
							}, 1000)
						} else {
							this.$vux.alert.show({
								title: '失败',
								content: '短信验证码发送失败'
							})
						}
					})
						.catch((err) => {
							console.log(err);
						})
				}
			} else {
				this.$vux.alert.show({
					title: '失败',
					content: '手机号码格式错误'
				})
			}

		},
		//绑定手机
		next() {
			this.$axios.post("/member_account-bind_mobile_step2.html", this.$qs.stringify({
				key: this.Key,
				auth_code: this.pho_code
			})
			).then((res) => {
				if (res.data.code == 200) {
					this.$vux.alert.show({
						title: '成功',
						content: '绑定成功'
					})
					setTimeout(() => {
						// location.href = this.ApiUrl + '/view/member/member_account.html';
						this.$router.push('/my');
					}, 2000);
				}
				else {
					this.$vux.alert.show({
						title: '失败',
						content: res.data.datas.error
					})
				}
			})
				.catch((err) => {
					console.log(err);
				})
		}
	}
}


