import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'

export default {
  data() {
    return {
      state: 1,
      title: '',
      orderNum: '5645216463554655685456',
      shopState: '待付款',
      codingState: '2',
      theResult: '',
      showNum: false,
      refund_id: this.$route.query.refund_id,//退款ID
      state: this.$route.query.state,//退款or退货
      codingTime: '2017-10-11 15:50',
      codingMoney: '632.0',
      goods_img_360:'',//商品图片
      goods_name:'',//商品名
      refund_sn:0,//退款编号
      reason_info:'',//退款信息
      seller_state:'',//商家审核
      admin_state:'',//平台确认
      seller_message:'',//处理结果
      refund_amount:'',//退款金额
      goods_num:0,//退款数量
      goods_spec:'',//规格
      show:false,
      showbtn:false,
      pic_list:[],//凭证列表
      goods_img_360:'',//商品图片
      goods_name:'',//商品名
      refund_sn:0,//退款编号
      reason_info:'',//退款信息
      seller_state:'',//商家审核
      admin_state:'',//平台确认
      seller_message:'',//处理结果
      refund_amount:'',//退款金额
      refund_num:0,//退款数量
      goods_spec:'',//规格
      show:false,
      showbtn:false,
      pic_list:[],//凭证列表
      Key: this.getCookie("key"),//用户唯一标识
      goods_img_360: '',//商品图片
      goods_name: '',//商品名
      refund_sn: 0,//退款编号
      reason_info: '',//退款信息
      seller_state: '',//商家审核
      admin_state: '',//平台确认
      seller_message: '',//处理结果
      refund_amount: '',//退款金额
      refund_num: 0,//退款数量
      goods_spec: '',//规格
      show: false,
      showbtn: false,
      pic_list: [],//凭证列表
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
  },
  computed: {

  },
  //页面载入
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.refundOrderDetail();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
    if (this.state == 1) {
      this.title = '退款详情';
      this.show = false;
      this.showbtn = false;
    } else {
      this.title = '退货详情';
      this.show = true;
      this.showbtn = true;
    }
  },
  methods: {
    //初始化数据
    InitParams: function () {
    },
    //退款订单详情
    refundOrderDetail: function () {
      this.$axios.get('/member_refund-get_refund_info.html', {
        params: {
          key: this.Key,
          refund_id: this.refund_id
        }
      }).then((res) => {
        if (res.data.code == 200) {
           let refund = res.data.datas.refund;
           this.goods_img_360 = refund.goods_img_360;
           this.goods_name = refund.goods_name;
           this.goods_num = res.data.datas.goods[0].goods_num;
           console.log(this.goods_num);
           this.refund_sn = refund.refund_sn;
           this.reason_info = refund.reason_info;
           this.seller_state = refund.seller_state;
           this.admin_state = refund.admin_state;
           this.seller_message = refund.seller_message;
           this.refund_amount = refund.refund_amount;
           this.goods_spec = refund.goods[0].goods_spec;
           console.log(this.goods_spec);
           //this.refund_num = refund.refund_num;
           this.pic_list = res.data.datas.pic_list;

        }
      })
        .catch((err) => {
          console.log(err);
        })
    },

    //退货发货
    returnShipping(value) {
      this.$router.push({ name: 'singleDetails', query: { refund_id: this.refund_id, refund_sn: value } });
    }

  },
}














