// 过滤器
export default {
    install(Vue, options) {
        //将时间戳转换成时间格式
        Vue.prototype.ChangeStampTime = function (el) {
            var date = new Date(el);
            var Y = date.getFullYear() + '-';
            var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
            var D = date.getDate() + ' ';
            var h = date.getHours() + ':';
            var m = date.getMinutes() + ':';
            var s = date.getSeconds();
            var newdate = Y + M + D + h + m + s;
            return newdate;
        };
    }
}



